<template>
    <div class="new-list latestdeal-order-box">
        <ul v-for="(item,key) in listdata" :title="item.type == 2 ? $lang.pengingorderBuyTitle : $lang.pengingorderSellTitle" :class="'flex-box depth-rect pad0-6 mouseover hover-ova-pad'+(newsdepth[key] ? (' animate-depth-'+(item.type == 1 ? 'up' : 'down')+animteChange):'')" :key="key">
            <li class="tl">
                <div :class="'bjs-color-'+(item.type == 1 ? 'up' : 'down')">{{ item.time }}</div>
            </li>
            <li class="tc">
                <div :class="'bjs-color-'+(item.type == 1 ? 'up' : 'down')" @click="setPrice(item.type,item.price,item.volume)">{{ item.price }}</div>
            </li>
            <li class="tr">{{ item.volume }}</li>
        </ul>
        <div v-show="list.length == 0 && !$store.state.socketState" class="trade-view-loading scale5"></div>
    </div>
</template>

<script>
export default {
    name: 'List',
    props: {
        list: {
            type: Array,
            default(){
                return []
            }
        }
    },
    data () {
        return {
            listdata: [],
            listbak: [],
            newsdepth: [],
            animteChange: ''
        }
    },
    watch: {
        list(){
            this.listdata = this.list
            //Save the value of each record -- the string
            let bak = []
            //Save whether each record changes
            let news = []
            this.list.forEach((elem) => {
                
                bak.push(JSON.stringify(elem))
            })
          
            if(this.listbak.length){
                for(let i = 0; i < bak.length; i++){
                    let temp = bak[i]
                    let tempStatus = 1
                    for(let j = 0; j < this.listbak.length; j++){
                        //If the comparison finds the same value in the list, the record is not updated.
                        if(temp == this.listbak[j]){
                            tempStatus = 0
                            break
                        }
                    }
                    // news = []
                    news.push(tempStatus)
                }
            }
            if(this.animteChange == ''){
                this.animteChange = 1
            }else{
                this.animteChange = ''
            }
            this.listbak = bak
            this.newsdepth = []
            setTimeout(() => {
                // this.newsdepth = []
                this.newsdepth = news
            },20)
            //this.newsdepth = news
            //console.log(this.listdata)
        }
    },
    methods: {
        setPrice(type,price,volume){
            type = type == 2 ? 'Buy' : 'Sell'
            this.$store.commit('setTradePrice',price)
            let _volume = volume
            //Sell
            this.$store.commit('setTradePrice',price)
            if(volume > this.$store.state.usd.coin){
                volume = this.$store.state.usd.coin
                if(volume == 0){
                    volume = ''
                }
            }
            this.$store.commit('setTradeVolumeSell',volume)
            //Buy
            if(_volume * price > this.$store.state.usd.usd){
                _volume = this.$store.state.usd.usd / price / 1.002
                if(_volume == 0){
                    _volume = ''
                }else{
                    _volume = Math.toFixed(_volume,this.$store.state.marketInfo.round)
                }
            }
            this.$store.commit('setTradeVolumeBuy',_volume)
            //this.$store.commit('setTradeVolume',volume)
            this.initMarket(price)
        },
        initMarket(price){
            let that = this
            let data = that.$store.state.marketInfo
            if(price.split('.')[1]){
                data.p_round = price.split('.')[1].length
            }else{
                data.p_round = 0
            }
            data.round = 8 - data.p_round
            let step = '1'
            if(data.round && data.round > 1){
                step = '0.'
                for(let i = 1; i < data.round; i++){
                    step += '0'
                }
                step += '1';
            }
            data.step = step
            let p_step = '1'
            if(data.p_round && data.p_round > 1){
                p_step = '0.'
                for(let i = 1; i < data.p_round; i++){
                    p_step += '0'
                }
                p_step += '1';
            }
            data.p_step = p_step
            that.$store.commit('setMarketInfo',data)
        }
    }
}
</script>


