<template>
    <div class="wrap">
        <Header :coinlist="$store.state.coinlist" />
        <Content/>

    </div>
</template>

<script>
import Header from './components/header/Index'
import Content from './components/content/Index'
import Footer from './components/footer/Index'

export default {
    name: 'App',
    components: {
        Header,
        Content,
        Footer
    },
    beforeCreate(){
        let that = this
        console.log(this)
        let skin = that.$store.state.skin
        that.$layer.apply({'skin': skin})
        if(skin == 'white'){
            document.body.className = 'skin-white'
        }else{
            document.body.className = ''
        }
        //Get market information
        that.axios.get(that.$store.state.localhost+'/Pro/coininfo?coinname='+that.$store.state.marketInfo.market).then((response) => {
            let data = response.data
            that.initMarket(data)
        })
        //Get user login information
        that.axios.get(that.$store.state.localhost+'/Pro/getuid/t/'+Math.floor(Math.random() * 1000000000000000000)).then((response) => {
            let data = response.data
            that.initLogin(data)
        })
		that.axios.get(that.$store.state.localhost+'/Pro/navbar').then((response) => {
		
            let data = response.data
            that.initNavigation(data)
        })
    },
    data(){
        return {
            openIframe: false
        }
    },
    methods: {
	initNavigation(data){

            let that = this
            if(data){
                that.$store.commit('setNavigation',data)
            }
        },
        initLogin(data){

            let that = this
            if(data.userid>0){
                that.$store.commit('setUserInfo',data)
                that.$store.commit('setLogin',!0)
                that.$store.state.update.getFav()
            }else{
                that.$store.commit('setLogin',!1)
            }
        },
        initMessage(){
            let that = this
            let iframe = that.$refs.messageIframe.contentWindow.document
            if(iframe.getElementById('clear-close')){
                iframe.getElementById('clear-close').addEventListener('click', function(){
                    that.openIframe = false
                })
            }
        },
        openMessage(){
            let that = this
            if(!that.$store.state.login){
                that.$layer.msg(that.$lang.tradeLogin,{time: 1000});
                return false;
            }
            that.openIframe = true
            setTimeout(function(){
                let iframe = that.$refs.messageIframe.contentWindow.document
                let $wrap = iframe.getElementById('websocket-cont-wrap')
                if($wrap.scrollTop == 0){
                    $wrap.scrollTop = $wrap.scrollHeight
                }				
            },10)
        },
        initMarket(data){
            let that = this
            //Construct a step based on the precision returned
            let step = '0.1'
            if(data.round && data.round > 1){
                step = '0.'
                for(let i = 1; i < data.round; i++){
                    step += '0'
                }
                step += '1';
            }
            data.step = step
            let p_step = '0.1'
            if(data.p_round && data.p_round > 1){
                p_step = '0.'
                for(let i = 1; i < data.p_round; i++){
                    p_step += '0'
                }
                p_step += '1';
            }
            data.p_step = p_step
            that.$store.commit('setMarketInfo',data)
            that.$store.commit('changeEntrustDepthRound',data.p_round)
            that.$store.commit('setInitMarket',!0)
            document.title = data.title
        }
    }
}
</script>



