class update {
    constructor(self) {
        this.self = self;
        this.socket = this.self.socket;
        this.init();
        this.initCoinlist = !1;
        this.setSocketAssetNum = 0;
    }
    init() {
        setInterval(() => {
            this.getCoinList();
            this.getPendings();
            this.getLatest();
        }, 3000);
        setInterval(() => {
            this.getCoinListSub();
        }, 6e4);
    }
    getCoinList() {
        let that = this.self;
        let coinlist = {};
        let newcoinlist = [];
        let fav = {};

        let data = {
            market: that.$store.state.marketInfo.market
        };
        that.axios
            .post(
                that.$store.state.localhost +
                    "/Pro/getJsonTop?market=" +
                    that.$store.state.marketInfo.market,
                data
            )
            .then(response => {
                coinlist = response.data.list;
                let round = that.$store.state.marketInfo.round;
                let p_round = that.$store.state.marketInfo.p_round;
                let topic = response.data.info;
                let socketTopic = {
                    amount: 0,
                    high: (topic.max_price * 1).toFixed(p_round),
                    id: 0,
                    low: (topic.min_price * 1).toFixed(p_round),
                    price: (topic.new_price * 1).toFixed(p_round),
                    rate: (topic.change * 1).toFixed(2),
                    volume: (topic.volume * 1).toFixed(round),
                    name: ""
                };
                for (let key in coinlist) {
                    if (key == data.market) {
                        socketTopic.id = coinlist[key].id;
                        that.$store.commit("changeSocketTopic", socketTopic);
                    }
                    coinlist[key].hide = !1;
                    let _key = key;
                    if (!fav[_key]) {
                        fav[_key] = !1;
                    }
                    newcoinlist.push(coinlist[key]);
                }
                that.$store.commit("changeSocketCoinList", newcoinlist);
                that.$store.commit("setCoinlist", coinlist);
                that.$store.commit("setFav", fav);
                that.initCoinlist = !0;
            });
    }
    getPendings() {
        let that = this.self;

        let data = {
            market: that.$store.state.marketInfo.market
        };
        that.axios
            .get(
                that.$store.state.localhost +
                    "/Pro/getPendingOrders?market=" +
                    that.$store.state.marketInfo.market
            )
            .then(pendingOrders => {
                let round = that.$store.state.marketInfo.round;
                let p_round = that.$store.state.marketInfo.p_round;
                if (pendingOrders.data.depth) {
                    this.setSocketAssetDepth(pendingOrders.data.depth);

                    if (pendingOrders.data.depth.buy) {
                        this.setSocketPendingOrderBuy(
                            pendingOrders.data.depth.buy
                        );
                    }

                    if (pendingOrders.data.depth.sell) {
                        this.setSocketPendingOrderSell(
                            pendingOrders.data.depth.sell
                        );
                    }
                    if (!pendingOrders.data.depth.buy) {
                        that.$store.commit("changeSocketPendingOrderBuy", []);
                    }
                    if (!pendingOrders.data.depth.sell) {
                        that.$store.commit("changeSocketPendingOrderSell", []);
                    }
                }
            });
    }
    getLatest() {
        let that = this.self;

        let data = {
            market: that.$store.state.marketInfo.market
        };
        that.axios
            .post(
                that.$store.state.localhost +
                    "/Pro/getTradelog?market=" +
                    that.$store.state.marketInfo.market,
                data
            )
            .then(response => {
                let round = that.$store.state.marketInfo.round;
                let p_round = that.$store.state.marketInfo.p_round;
                if (response.data.tradelog) {
                    this.setSocketLatestdeal(response.data.tradelog);
                }
            });
    }
    getCoinListSub() {
        let that = this.self;
        let coinlist = {};
        let newcoinlist = [];
        let fav = {};

        let data = {
            market: that.$store.state.marketInfo.market
        };
        that.axios
            .post(that.$store.state.localhost + "/Pro/getJsonTop", data)
            .then(response => {
                coinlist = response.data.list;
                let round = that.$store.state.marketInfo.round;
                let p_round = that.$store.state.marketInfo.p_round;
                let topic = response.data.info;
                let socketTopic = {
                    amount: 0,
                    high: (topic.max_price * 1).toFixed(p_round),
                    id: 0,
                    low: (topic.min_price * 1).toFixed(p_round),
                    price: (topic.new_price * 1).toFixed(p_round),
                    rate: (topic.change * 1).toFixed(2),
                    volume: (topic.volume * 1).toFixed(round),
                    name: ""
                };
                for (let key in coinlist) {
                    if (key == data.market) {
                        socketTopic.id = coinlist[key].id;
                        that.$store.commit("changeSocketTopic", socketTopic);
                    }
                    coinlist[key].hide = !1;
                    let _key = key;
                    if (!fav[_key]) {
                        fav[_key] = !1;
                    }
                    newcoinlist.push(coinlist[key]);
                }
                that.$store.commit("changeSocketCoinList", newcoinlist);
            });
    }
    getFav() {
        //console.log("called");
        let that = this.self;
        let data = {
            userId: that.$store.state.userInfo.userid,
            token: that.$store.state.userInfo.token
        };
        if (!that.$store.state.userInfo.userid) {
            return !1;
        }
        let fav = {};
        let coinlist = that.$store.state.socketCoinList;
        for (let key in coinlist) {
            if (!fav[key]) {
                fav[key] = !1;
            }
        }
        that.axios
            .post(
                that.$store.state.servicehost + "/user/v1star/action/find",
                data
            )
            .then(response => {
                if (response.data.data && response.data.data.length) {
                    response.data.data.market.forEach(elem => {
                        fav[elem.coin] = 1;
                    });
                }
                that.$store.commit("setFav", fav);
            });
    }
    onMessage(data) {
        let that = this.self;
        //console.log(data)
        if (data.type == "ping") {
            //console.log('ping:3663')
            //this.sendMessage({ cmd: 'ping', id: 3663, args: [parseInt(Date.now() / 1000)] })
        }
        if (data.r == "coinlist" && data.s && data.list) {
            if (this.initCoinlist) {
                this.setSocketCoinList(data.list);
            } else {
                setTimeout(() => {
                    this.setSocketCoinList(data.list);
                }, 1000);
            }
        }
        if (data.r == "exchange" && data.s && data.list) {
            this.setSocketLatestdeal(data.list);
        }
        if (data.r == "entrust" && data.s) {
            if (data.buy && data.buy.length) {
                this.setSocketPendingOrderBuy(data.buy);
            }
            if (data.sell && data.sell.length) {
                this.setSocketPendingOrderSell(data.sell);
            }
            if (!data.buy) {
                that.$store.commit("changeSocketPendingOrderBuy", []);
            }
            if (!data.sell) {
                that.$store.commit("changeSocketPendingOrderSell", []);
            }
        }
        if (data.r == "userInfo" && data.s) {
            this.setSocketAsset(data);
        }
        if (data.r == "depth" && data.s) {
            this.setSocketAssetDepth(data);
        }
    }
    setSocketLatestdeal(data) {
        let that = this.self;
        let listdata = that.$store.state.socketLatestdeal;
        let round = that.$store.state.marketInfo.round;
        let p_round = that.$store.state.marketInfo.p_round;
        for (let k = 0; k < data.length; k++) {
            let latestdeal = {};
            latestdeal.type = data[k].type;
            latestdeal.time = data[k].time;
            latestdeal.price = (data[k].price * 1).toFixed(p_round);
            latestdeal.volume = (data[k].num * 1).toFixed(round);
            latestdeal.amount = (data[k].price * data[k].num * 1).toFixed(2);

            listdata.unshift(latestdeal);
            if (listdata.length > 35) {
                listdata.splice(listdata.length - 1, 1);
            }
        }
        that.$store.commit("changeSocketLatestdeal", listdata);
        that.$store.commit("setTradeNicePrice", data[data.length - 1].price);
    }
    setSocketPendingOrderBuy(data) {
        let that = this.self;
        let listdata = []; //that.$store.state.socketPendingOrderBuy
        let round = that.$store.state.marketInfo.round;
        let p_round = that.$store.state.marketInfo.p_round;
        let d_round = that.$store.state.entrustDepth;
        d_round = d_round < 0 ? 0 : d_round;
        for (let k = 0; k < data.length; k++) {
            let pendingOrderBuy = {};
            pendingOrderBuy.price = (data[k].price * 1).toFixed(d_round);
            pendingOrderBuy.volume = (data[k].volume * 1).toFixed(round);
            pendingOrderBuy.amount = (data[k].amount * 1).toFixed(2);
            listdata.push(pendingOrderBuy);
            if (listdata.length > 30) {
                listdata.splice(30, 1);
            }
        }
        that.$store.commit("changeSocketPendingOrderBuy", listdata);
        if (that.$store.state.tradeNicePriceSell == 0) {
            that.$store.commit(
                "setTradeNicePriceSell",
                (listdata[0].price * 1).toFixed(p_round)
            );
        }
    }
    setSocketPendingOrderSell(data) {
        let that = this.self;
        let listdata = []; //that.$store.state.socketPendingOrderSell
        let round = that.$store.state.marketInfo.round;
        let p_round = that.$store.state.marketInfo.p_round;
        let d_round = that.$store.state.entrustDepth;
        d_round = d_round < 0 ? 0 : d_round;
        for (let k = data.length - 1; k >= 0; k--) {
            let pendingOrderSell = {};
            pendingOrderSell.price = (data[k].price * 1).toFixed(d_round);
            pendingOrderSell.volume = (data[k].volume * 1).toFixed(round);
            pendingOrderSell.amount = (data[k].amount * 1).toFixed(2);
            listdata.push(pendingOrderSell);
            if (listdata.length > 30) {
                listdata.splice(0, 1);
            }
        }
        that.$store.commit("changeSocketPendingOrderSell", listdata);
        if (that.$store.state.tradeNicePriceBuy == 0) {
            //that.$store.commit('setTradeNicePriceBuy',(listdata[listdata.length-1].price*1).toFixed(p_round))
        }
    }
    setSocketCoinList(data) {
        let that = this.self;
        let coinlist = that.$store.state.socketCoinList;
        let market = that.$store.state.marketInfo.market;
        let socketTopic = that.$store.state.socketTopic;
        let round = that.$store.state.marketInfo.round;
        let p_round = that.$store.state.marketInfo.p_round;
        let list = coinlist;
        list.forEach(elem => {
            for (let i = 0; i < data.length; i++) {
                let coin = data[i];
                if (elem.id == coin.id) {
                    elem.change = (coin.rate * 100).toFixed(2);
                    elem.new_price = coin.price;
                    if (elem.name == market) {
                        socketTopic.volume = (coin.volume * 1).toFixed(p_round);
                        socketTopic.rate = (coin.rate * 100).toFixed(2);
                        socketTopic.price = (coin.price * 1).toFixed(p_round);
                        socketTopic.low = (coin.low * 1).toFixed(p_round);
                        socketTopic.high = (coin.high * 1).toFixed(p_round);
                        socketTopic.amount = (coin.amount * 1).toFixed(p_round);
                    }
                }
            }
        });
        that.$store.commit("changeSocketTopic", socketTopic);
        that.$store.commit("changeSocketCoinList", coinlist);
    }
    setSocketAsset(data) {
        let that = this.self;
        let list = that.$store.state.socketAsset;
        if (!data.addEntrust && !data.removeEntrust && !data.updateEntrust) {
            return !1;
        }
        if (!list.length && that.setSocketAssetNum < 10) {
            //Maybe we haven't got the data from ajax yet waiting for 10s
            that.setSocketAssetNum++;
            setTimeout(() => {
                this.setSocketAsset(data);
            }, 1e3);
            return !1;
        }

        let round = that.$store.state.marketInfo.round;
        let p_round = that.$store.state.marketInfo.p_round;

        if (data.addEntrust && data.addEntrust.length) {
            for (var i = 0; i < data.addEntrust.length; i++) {
                let elem = data.addEntrust[i];
                let obj = {
                    id: elem.id,
                    time: this.formatFullTime(elem.time),
                    type: elem.type == 0 ? 1 : 2,
                    price: (elem.price * 1).toFixed(p_round),
                    volume: (elem.num * 1).toFixed(round),
                    amount: (elem.num * elem.price).toFixed(p_round)
                };
                list.unshift(obj);
            }
        }

        if (data.removeEntrust && data.removeEntrust.length) {
            for (var i = 0; i < data.removeEntrust.length; i++) {
                let elem = data.removeEntrust[i];
                for (let j = 0; j < list.length; j++) {
                    if (list[j].id == elem) {
                        list.splice(j, 1);
                    }
                }
            }
        }

        if (data.updateEntrust && data.updateEntrust.length) {
            for (var i = 0; i < data.updateEntrust.length; i++) {
                let elem = data.updateEntrust[i];
                for (let j = 0; j < list.length; j++) {
                    if (list[j].id == elem.id) {
                        list[j].volume = (elem.deal * 1).toFixed(round);
                        list[j].amount = (
                            list[j].volume * list[j].price
                        ).toFixed(p_round);
                    }
                }
            }
        }
        that.$store.commit("changeSocketAsset", list);
    }
    setSocketAssetDepth(data) {
        let that = this.self;
        if (!data.price) {
            data.price = 0;
        }
        if (!data.buy) {
            data.buy = [{ price: data.price * 0.99, volume: 0 }];
        }
        if (!data.sell) {
            data.sell = [{ price: data.price * 1.01, volume: 0 }];
        }

        // let buy_min_price = ((data.price*0.99).toFixed(4))*1
        // let sell_max_price = ((data.price*1.01).toFixed(4))*1
        // if(data.buy.length){
        //     if(buy_min_price != data.buy[data.buy.length-1].price){
        //         data.buy.push({
        //             price: buy_min_price,
        //             volume: 0
        //         })
        //     }
        // }
        // if(data.sell.length){
        //     if(sell_max_price != data.sell[data.sell.length-1].price){
        //         data.sell.push({
        //             price: sell_max_price,
        //             volume: 0
        //         })
        //     }
        // }

        let buy = [];
        let sell = [];
        let volume_buy_sum = 0;
        let volume_sell_sum = 0;
        for (let i = 0; i < data.buy.length; i++) {
            let elem = data.buy[i];
            volume_buy_sum += elem.volume;
            buy.push({
                price: elem.price,
                volume: (volume_buy_sum * 1).toFixed(4)
                //volume:elem.volume
            });
        }
        for (let i = 0; i < data.sell.length; i++) {
            let elem = data.sell[i];
            volume_sell_sum += elem.volume;
            sell.push({
                price: elem.price,
                volume: (volume_sell_sum * 1).toFixed(4)
                //volume:elem.volume
            });
        }
        data.buy = buy;
        data.sell = sell;
        //console.log('global_update.js')
        that.$store.commit("changeSocketDepth", data);
    }
    sendMessage(data) {
        if (!this.socket) {
            this.socket = this.self.socket;
        }
        if (this.socket.checkOpen()) {
            this.socket.send(data);
        } else {
            this.socket.on("open", () => {
                this.socket.send(data);
            });
        }
    }
    formatTime(time) {
        let date = new Date(time);
        let h = date.getHours();
        let m = date.getMinutes();
        let s = date.getSeconds();
        h = h < 10 ? "0" + h : h;
        m = m < 10 ? "0" + m : m;
        s = s < 10 ? "0" + s : s;
        return h + ":" + m + ":" + s;
    }
    formatFullTime(time) {
        let date = new Date(time);
        let Y = date.getFullYear();
        let M = date.getMonth() + 1;
        let D = date.getDate();
        let h = date.getHours();
        let m = date.getMinutes();
        let s = date.getSeconds();
        M = M < 10 ? "0" + M : M;
        D = D < 10 ? "0" + D : D;
        h = h < 10 ? "0" + h : h;
        m = m < 10 ? "0" + m : m;
        s = s < 10 ? "0" + s : s;
        return Y + "-" + M + "-" + D + " " + h + ":" + m + ":" + s;
    }
}

export default update;



