// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import store from './store'
import './global/insertSort'
import './global/toFixed'
import './global/roundRect'
import './assets/css/index.css'
import './assets/css/white.css'
import './assets/css/stats.css'
import './assets/css/intlTelInput.css'
import Cookies from  './global/cookies'
import 'babel-polyfill'
import App from './App'
import axios from 'axios'
import VueAxios from 'vue-axios'
import qs from 'qs'
import md5 from 'js-md5'
import LangEn from './global/en-us'
import LangZh from './global/zh-cn'

import layer from 'easy-layer'

Vue.use(layer)

axios.defaults.headers.post['Accept'] = 'application/json, text/javascript, */*; q=0.01';
axios.defaults.headers.post['X-Requested-With'] = 'xmlhttprequest'
axios.defaults.headers.post['Content-Type'] = 'application/x-www-form-urlencoded; charset=UTF-8';
axios.interceptors.request.use((config) => {
	config.data = qs.stringify(config.data, {arrayFormat: 'brackets'})
	return config
})


Vue.use(VueAxios, axios)
Vue.prototype.$md5 = md5
Vue.prototype.$Cookies = Cookies

let language = Cookies.get('think_language') || 'en-us'

Vue.prototype.$lang = language == 'en-us' ? LangEn : LangZh

Vue.config.productionTip = false

/* eslint-disable no-new */
new Vue({
	el: '#app',
	components: { App },
	store,
	template: '<App/>'
})



