<template>
    <div id="chart_box" ref="chart_box">
        <canvas id="chart" ref="chart"></canvas>
        <canvas id="chart_mask" ref="chart_mask"></canvas>
        
    </div>
</template>

<script>
export default {
    name: 'Depth',
    props: {
        datalist: {
            type: Object,
            default(){
                return {
                    sell: [],
                    buy: [],
                    price: 0
                }
            }
        },
        resize: {
            type: Number,
            default: 0
        },
        skin: {
            type: String,
            default: 'white'
        },
        show: {
            type: Number,
            default: 1
        }
    },
    mounted(){
        this.getData()
    },
    data () {
        return {
            $box: null,
            $chart: null,
            $mask: null,
            canvas1: null,
            ctx1: null,
            canvas2: null,
            ctx2: null,
            cwidth: null,
            cheight: null,
            now_price: 0,
            newAxis: null,
            config: {
                white: {
                    bg: '#ffffff',
                    color: '#666',
                    circle: 'rgba(0,182,183,0.5)',
                    shadowColor: '#aaa',
                    lineColor: '#9194a4'
                },
                black: {
                    bg: '#1f2941',
                    color: '#aaa',
                    circle: 'rgba(0,255,255,0.5)',
                    shadowColor: '#000',
                    lineColor: '#41486A'
                }
            }
        }
    },
    watch: {
        resize(){
            if(this.resize > 0){
                this.getData()
            }
        },
        skin(){
            this.getData()
        },
        datalist(){
            this.getData()
        },
        show(){
            this.getData()
        }
    },
    methods: {
        getData(){
            let that = this
            if(that.show != 2){
                return !1
            }
            let data = that.datalist
            if((data.buy && data.buy.length) || (data.sell.length && data.sell)){
                
				that.init();
				
            }
        },
        init(){
            let that = this
            let t0 = new Date().getTime()
            that.$box = that.$refs.chart_box
            that.$chart = that.$refs.chart
            that.$mask = that.$refs.chart_mask
            that.canvas1 = that.$chart
            that.canvas2 = that.$mask
            that.canvas1.width = that.$box.offsetWidth
            that.canvas1.height = that.$box.offsetHeight
            that.ctx1 = that.canvas1.getContext('2d')
            that.canvas2.width = that.$box.offsetWidth
            that.canvas2.height = that.$box.offsetHeight
            that.ctx2 = that.canvas2.getContext('2d')
            that.cwidth = that.canvas2.width
            that.cheight = that.canvas2.height - 23
			
			
            let data = that.datalist
			
            let chartsData = that.initAxis(data)
            that.cwidth = that.canvas2.width - ((that.ctx1.measureText(chartsData.axis.y1.toFixed(2)).width) >> 0) - 12;
            that.draw(chartsData);

            let oL = that.offset(that.$mask).left
            let oT = that.offset(that.$mask).top
            that.$mask.onmousemove = e =>{
                let _oL = e.pageX
                let _oT = e.pageY
                let edata = {x:_oL-oL,y:_oT-oT}
                that.drawMask(edata,chartsData)
            }
            that.$mask.onmouseout = e => {
                that.ctx2.clearRect(0,0,that.canvas2.width,that.canvas2.height)
            }
            let t1 = new Date().getTime()
            console.log('>> canvas time: ',t1-t0)
        },
        draw(data){
            let that = this
            that.ctx1.clearRect(0,0,that.canvas1.width,that.canvas1.height)
            that.ctx1.fillStyle = that.config[that.skin].bg
            that.ctx1.fillRect(0,0,that.canvas1.width,that.canvas1.height)
            that.newAxis = that.drawAxis(data.axis)   
            that.drawBuys(data,that.newAxis)
            that.drawSells(data,that.newAxis)
			
        },
        drawAxis(data){
            let that = this
            that.ctx1.strokeStyle = that.config[that.skin].lineColor
            that.ctx1.lineWidth = 1
            that.ctx1.beginPath()
            that.ctx1.translate(0.5,0.5)
            that.ctx1.moveTo(0,that.cheight)
            that.ctx1.lineTo(that.cwidth,that.cheight)
            that.ctx1.lineTo(that.cwidth,0)
            that.ctx1.stroke()
            that.ctx1.closePath()
            //Font size, color
            that.ctx1.font = '12px'
            that.ctx1.fillStyle = that.config[that.skin].lineColor    
            //x, y coordinate
            let xAxis = [], yAxis = []
            //Number of divisions
            let xL = 5, yL = 6
            //Split distance
            let xN = that.cwidth / xL
            let yN = that.cheight / yL
            //X-axis segmentation
            let _xAxis = data.x1 - data.x0
            for(let i = 0; i < xL; i++){
                xAxis.push(((data.x0 + _xAxis/xL * (i+0.5))*1).toFixed(that.$store.state.marketInfo.p_round))
            }
            //console.log(xAxis)
            //Y-axis segmentation
            let _yAxis = data.y1
            for(let i = 0; i < yL; i++){
                yAxis.push((_yAxis/yL * i).toFixed(2))
            }
            let yAxis2 = []
            if(yAxis[yAxis.length-1]*1 > 1000){
                for(let i = 0; i < yAxis.length; i++){
                    yAxis2.push(that.format(yAxis[i]))
                }
            }else{
                yAxis2 = yAxis
            }
            //X-axis traces, coordinates
            for(let i = 0; i < xAxis.length; i++){
                that.ctx1.fillText(xAxis[i],xN*(i+0.5)-that.ctx1.measureText(xAxis[i]).width/2,that.cheight+16)
                that.ctx1.beginPath()
                that.ctx1.moveTo(xN*(i+0.5),that.cheight+1)
                that.ctx1.lineTo(xN*(i+0.5),that.cheight+5)
                that.ctx1.stroke()
                that.ctx1.closePath()
            }
            //Y-axis traces, coordinates
            for(let i = 0; i < yAxis.length; i++){
                that.ctx1.fillText(yAxis2[i],that.cwidth+6,that.cheight-yN*i+4)
                that.ctx1.beginPath()
                that.ctx1.moveTo(that.cwidth,that.cheight-yN*i)
                that.ctx1.lineTo(that.cwidth+4,that.cheight-yN*i)
                that.ctx1.stroke()
                that.ctx1.closePath()
            }
            that.ctx1.translate(-0.5,-0.5)
            return {xL: xL, yL: yL, xN: xN, yN: yN, xAxis: xAxis, yAxis: yAxis, _xAxis: _xAxis, _yAxis: _yAxis}
        },
        drawBuys(data,newAxis){
            let that = this
            that.ctx1.fillStyle = 'rgba(60,181,149,0.2)'
            let buys = data.buys
            that.ctx1.lineWidth = 1
            that.ctx1.beginPath()
            //that.ctx1.moveTo(0,that.cheight)
            let sell = {x: 0, y: 0}
            let temp = {x: 0, y: 0}
            let begin = !0
            for(let i = 0; i < buys.length; i++){
                sell = that.transform(buys[i],data.axis,newAxis)
                if(begin && buys[i].volume != 0){
                    begin = !1
                    that.ctx1.moveTo(sell.x,that.cheight)
                }
                if(buys[i].volume != 0){
                    that.ctx1.lineTo(sell.x,sell.y)
                    temp = sell
                }
            }
            //that.ctx1.lineTo(that.cwidth/2,that.cheight)
            that.ctx1.lineTo(temp.x,that.cheight)
            that.ctx1.closePath()
            that.ctx1.fill()
        },
        drawSells(data,newAxis){
            let that = this
            that.ctx1.fillStyle = 'rgba(248,129,132,0.2)'
            let sells = data.sells
            that.ctx1.lineWidth = 1
            that.ctx1.beginPath()
            //that.ctx1.moveTo(that.cwidth/2,that.cheight)
            let sell = {x: 0, y: 0}
            let temp = {x: 0, y: 0}
            let begin = !0
            for(let i = 0; i < sells.length; i++){
                sell = that.transform(sells[i],data.axis,newAxis)
                if(begin && sells[i].volume != 0){
                     begin = !1
                    that.ctx1.moveTo(sell.x,that.cheight)
                }
                if(sells[i].volume != 0){
                    that.ctx1.lineTo(sell.x,sell.y)
                    temp = sell
                }
            }
            that.ctx1.lineTo(temp.x,that.cheight)
            that.ctx1.closePath()
            that.ctx1.fill()
        },
        initAxis(data){
            let that = this
            let allData = []
            let prices = [], volumes = []//Total price and transaction volume
            let sells = [], buys = []//Buy and sell sort
                sells = data.sell.insertSort(function(a,b){
                    return a.price > b.price
                })
                buys = data.buy.insertSort(function(a,b){
                    return a.price > b.price
                })
            for(let j in sells){
                prices.push(sells[j].price)
                volumes.push(sells[j].volume)
                allData.push({price: sells[j].price, volume: sells[j].volume})
            }
			
			
            prices.push(data.price)
            volumes.push(0)
            allData.push({price: data.price, volume: 0})
            for(let j in buys){
                prices.push(buys[j].price)
                volumes.push(buys[j].volume)
                allData.push({price: buys[j].price, volume: buys[j].volume})
            }
                prices.insertSort(function(a,b){
                    return a > b
                })
                volumes.insertSort(function(a,b){
                    return a > b
                })
                allData.insertSort(function(a,b){
                    return a.price > b.price
                })
            return {axis: {x0: prices[0], x1: prices[prices.length-1], y0: volumes[0], y1: volumes[volumes.length-1]*1.1}, sells: sells, buys: buys, prices: prices, volumes: volumes, allData: allData}
        },
        transform(data,axis,newAxis){
            let that = this
            let xVal = newAxis._xAxis
            let yVal = newAxis._yAxis
            //Number axis size - pixel
            let xPX = that.cwidth
            let yPX = that.cheight
            //Current coordinate--value
            let x = data.price - axis.x0
            let y = data.volume
            //Converted coordinates - pixels
            let _x = (xPX/xVal*x).toFixed(2)
            let _y = (yPX-yPX/yVal*y).toFixed(2)
            return {x: _x, y: _y}
        },
        drawMask(edata,chartsData){
            let that = this
            let x0 = chartsData.axis.x0
            //End point - value
            let x1 = chartsData.axis.x1
            //Number axis length - value
            let xVal = x1 - x0
            //Price and quantity
            let prices = chartsData.prices
            let volumes = chartsData.volumes
            let allData = chartsData.allData
            //Current mouse position
            let x = edata.x
            let y = edata.y
            //Conversion
            let price = x / that.cwidth * xVal + x0
            let volume = volumes[0]
            let index = 0
            for(let i = 0; i < prices.length; i++){
                if(i == 0 && price >= prices[i]){
                    index = i
                }
                if(i > 0 && price >= (prices[i] + prices[i - 1])/2){
                    index = i
                }
            }

            that.ctx2.clearRect(0,0,that.canvas2.width,that.canvas2.height)
            if(x < 0 || x > that.cwidth || allData[index].price == 0 || allData[index].volume == 0){
                return false
            }
            //Number of divisions
            let xL = 5, yL = 6
            let rx,ry,rr
            rx = (prices[index] - x0) / xVal * that.cwidth
            ry = that.cheight - allData[index].volume / (volumes[volumes.length - 1]*1.1) * that.cheight
            rr = 3
            that.ctx2.beginPath()
            that.ctx2.fillStyle = that.config[that.skin].circle
            that.ctx2.strokeStyle = that.config[that.skin].circle
            that.ctx2.arc(rx,ry,rr,0,2*Math.PI)
            that.ctx2.stroke()
            that.ctx2.fill()
            that.ctx2.beginPath()
            that.ctx2.arc(rx,ry,rr*2,0,2*Math.PI)
            that.ctx2.stroke()
            that.ctx2.fill()

            let _rx,_ry,_rw,_rh
            _rw = 160
            _rh = 60
            _rx = rx - _rw / 2
            _ry = ry - _rh - 10
            _rx = _rx < 2 ? 2 : (_rx > that.cwidth - 2 - _rw ? that.cwidth - 2 - _rw : _rx)
            _ry = _ry < 2 ? ry + 12 : _ry
            that.ctx2.beginPath()
            that.ctx2.roundRect(_rx, _ry, _rw, _rh, 3)
            that.ctx2.shadowBlur = 2
            that.ctx2.shadowOffsetY = 1
            that.ctx2.shadowColor = that.config[that.skin].shadowColor
            that.ctx2.fillStyle = that.config[that.skin].bg
            that.ctx2.fill()

            that.ctx2.beginPath()
            let txt = that.$lang.coinlistPrice+''+allData[index].price
            let txt2 = that.$lang.depthAmount+''+allData[index].volume
            that.ctx2.shadowBlur = 0
            that.ctx2.shadowOffsetY = 0
            that.ctx2.shadowColor = 'transparent'
            that.ctx2.font = '18px'
            that.ctx2.fillStyle = that.config[that.skin].color
            that.ctx2.fillText(txt,_rx+10,_ry+25)
            that.ctx2.fillText(txt2,_rx+10,_ry+45)
        },
        format(num){
            num = num * 1
            if(num < 1000*1000){
                return (num/1000).toFixed(2)+'K'
            }
            if(num >= 1000*1000 && num < 1000*1000*1000){
                return (num/1000/1000).toFixed(2)+'M'
            }
            if(num >= 1000*1000*1000){
                return (num/1000/1000/1000).toFixed(2)+'B'
            }
        },
        offset(elem){
            let obj = {
                left: elem.offsetLeft,
                top: elem.offsetTop,
                width: elem.offsetWidth,
                height:  elem.offsetHeight
            }
           while(elem != document.body){
                elem = elem.offsetParent
                obj.left += elem.offsetLeft
                obj.top += elem.offsetTop
            }
            return obj
        }
    }
}
</script>

<style type="text/css">
#chart_box{position: relative; height: 100%; width: 100%; overflow: hidden;}
#chart,#chart-mask{display: block;}
#chart_mask{position: absolute; left: 0; top: 0;}
</style>


