<template>
    <div class="my-tab-box trade-unit-box clearfix roundbox">
	<span class="move"><img src="@/assets/images/move.svg"></span>
	    <div class="my-tab-head w100-15 tophdark">
			<span :class="(tabs.asset == 0 ? 'active ':'')+'mouseover darkgray'" @click="tabChange(0)">{{ $lang.assetOpenOrder }}</span>
            <span :class="(tabs.asset == 1 ? 'active ':'')+'mouseover darkgray'" @click="tabChange(1)">{{ $lang.assetHistoryOrder }}</span>
	    </div>
		<div class="my-tab-cont my-tab-cont-2-3 w100-15">
            <Entrust v-show="tabs.asset == 0" :datalist="$store.state.socketAsset" />
            <Traded v-show="tabs.asset == 1" :datalist="$store.state.tradeLog" />
		</div>
    </div>
</template>

<script>
import Entrust from './Entrust'
import Traded from './Traded'
export default {
    name: 'Asset',
    components: {
    	Entrust,
    	Traded
    },
    data () {
        return {
        	tabs: {
        		asset: 1
        	}
        }
    },
    methods: {
        tabChange(asset){
            if(asset == this.tabs.asset){
                return !1
            }
            this.tabs.asset = asset
            this.$store.commit('addResize')
        }
    }
}
</script>



