<template>
	<button @click="click" :class="classname + (loading ? ' loading-btn' : '')" :title="title">{{ text }}</button>
</template>

<script>
export default {
    name: 'Button',
    props: {
    	'text': {
    		type: String,
    		default: 'button'
    	},
    	'tap': {
    		type: Function,
    		default(callback){
    			callback()
    		}
    	},
    	'type': {
    		type: String,
    		default: 'blue'
    	},
    	'title': {
    		type: String,
    		default: ''
    	}
    },
    data () {
    	let type = this.type.split(/\./)
    	let btns = ''
    	type.forEach(elem => {
    		btns += ' bjs-btn-' + elem
    	})
        return {
        	loading: !1,
        	classname: 'bjs-btn' + btns
        }
    },
    methods: {
    	click(){
    		if(this.loading){
    			return !1
    		}
    		this.$emit('tap',this)
    	}
    }
}
</script>



