<template>
    <div class="header">
        <div class="header-menu">
            <div class="window-wrap clearfix">
                <div class="pull-left home-logo">
                    <a :href="$store.state.localhost + '/'"></a>
                </div>
                <div class="">
                    <ul class="header-menus pull-left">
                
         
                        <li
                            v-for="(item, key) in $store.state.navigation"
                            :key="key" class="item-submenu"
                        >
                            <a :href="$store.state.localhost + '/' + item.url"
                                ><span class="flex-caret">{{ item.title }} <em v-if="item.submenu" class="show-caret"></em></span></a
                            >
                           <ul class="dropdown">
                         <li v-for="(item1, key1) in item.submenu"
                            :key="key1"><a href="#">{{ item1.title }}</a></li>
                         
                       </ul>
                           
                        </li>
                    </ul>

                    <ul class="pull-right login_lan trade_login_lan">
                        <li class="style-box">
                            <span>{{ $lang.menuSkin }}</span>
                            <span
                                :class="
                                    ($store.state.skin == 'white'
                                        ? 'active '
                                        : '') + 'style-white'
                                "
                                @click="changeSkin('white')"
                            ></span>
                            <span
                                :class="
                                    ($store.state.skin == 'black'
                                        ? 'active '
                                        : '') + 'style-black'
                                "
                                @click="changeSkin('black')"
                            ></span>
                        </li>
                        <li class="lang-option">
                            <div class="text-nowrap">
                                <span
                                    :class="
                                        'iti-flag ' +
                                            ($store.state.language == 'zh-cn'
                                                ? $lang.langZhclass
                                                : $lang.langEnclass)
                                    "
                                ></span
                                >{{
                                    $store.state.language == "zh-cn"
                                        ? $lang.langZh
                                        : $lang.langEn
                                }}
                            </div>
                            <div ref="lang_box" class="more-lan">
                                <a
                                    @click="changeLang('zh-cn')"
                                    class="text-nowrap"
                                    href="javascript:;"
                                    ><span
                                        :class="'iti-flag ' + $lang.langZhclass"
                                    ></span
                                    >{{ $lang.langZh }}</a
                                >
                                <a
                                    @click="changeLang('en-us')"
                                    class="text-nowrap"
                                    href="javascript:;"
                                    ><span
                                        :class="'iti-flag ' + $lang.langEnclass"
                                    ></span
                                    >{{ $lang.langEn }}</a
                                >
                            </div>
                        </li>
                    </ul>
                    <div class="pull-right user_info_box">
                        <ul
                            v-show="$store.state.login"
                            :class="
                                (!$store.state.login ? 'hide ' : '') +
                                    'pull-right user_info_list login_lan'
                            "
                            id="login-ing"
                        >
                            <li class="login-out pull-right">
                                <a
                                    :href="
                                        $store.state.localhost +
                                            '/Login/loginout'
                                    "
                                    >{{ $lang.menuLogout }}</a
                                >
                            </li>
                            <li class="user-info pull-right">
                                <a
                                    class="user-name"
                                    title=""
                                    :href="$store.state.localhost + '/User/'"
                                    ><span
                                        ><em id="username">{{
                                            $store.state.userInfo.nickName
                                        }}</em></span
                                    ></a
                                >
                                <div class="user-info-list otc-trade-more-box">
                                    <div
                                        class="clearfix text-nowrap"
                                        v-if="$store.state.userInfo"
                                    >
                                        <div class="user-info-item">
                                            <h4>
                                                {{ $lang.menuBalance }}
                                                <strong id="userCoin">{{
                                                    $store.state.userInfo.usd
                                                }}</strong>
                                                {{ usd.toLocaleUpperCase() }}
                                            </h4>
                                        </div>
                                        <div class="user-info-item">
                                            <h4>
                                                {{ $lang.menuFrozen }}<strong
                                                    id="userCoinUsdd"
                                                    >{{
                                                        $store.state.userInfo
                                                            .usdd
                                                    }}</strong
                                                >
                                                {{ usd.toLocaleUpperCase() }}
                                            </h4>
                                        </div>
                                    </div>
                                </div>
                            </li>
                        </ul>

                        <ul
                            :class="
                                (!$store.state.login ? '' : 'hide ') +
                                    'header-login pull-left'
                            "
                            id="login-out"
                        >
                            <li class="pull-right">
                                <a
                                    class="active"
                                    :href="
                                        $store.state.localhost +
                                            '/Login/register'
                                    "
                                    id="registerBtn"
                                    ><span>{{ $lang.menuRegister }}</span></a
                                >
                            </li>
                            <li class="pull-right">
                                <a
                                    :href="
                                        $store.state.localhost + '/Login/login'
                                    "
                                    id="loginBtn"
                                    ><span>{{ $lang.menuLogin }}</span></a
                                >
                            </li>
                        </ul>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "Header",
    props: {
        coinlist: {
            type: Object,
            default() {
                return {};
            }
        }
    },
    data() {
        return {
            someList: {},
            orderList: {},
            lang: this.$store.state.lang,
            usd: this.$store.state.marketInfo.market.split("_")[1]
        };
    },
    watch: {
        coinlist() {
            let data = this.coinlist;
            let that = this;
            for (let i in data) {
                let type = data[i]["name"].split("_")[1];
                if (!that.someList[type]) {
                    that.$set(that.someList, type, []);
                }
                that.someList[type].push(data[i]);
            }
        }
    },
    mounted() {
        let skin = this.$store.state.skin;
        this.changeSkin(skin);
    },
    methods: {
        changeSkin(skin) {
            if (skin == this.$store.state.skin) {
                return !1;
            }
            if (this.$store.state.tradingview_config.loading) {
                this.$store.state.tradingview_config.resetTheme(skin);
            }
            this.$store.commit("changeSkin", skin);
            //this.$store.state.skin = skin
            this.$layer.apply({ skin: skin });
            this.$Cookies.set("skin", skin, 7);
            if (skin == "white") {
                document.body.className = "skin-white";
            } else {
                document.body.className = "";
            }
        },
        changeLang(lang) {
            if (this.$store.state.language != lang) {
                this.$Cookies.set("think_language", lang, 7);
                window.location.reload();
            }
            this.$refs.lang_box.classList.add("hide");
            setTimeout(() => {
                this.$refs.lang_box.classList.remove("hide");
            }, 300);
        }
    }
};
</script>



