<template>
	<div :class="(coinlist.length == 0 ? 'min-h60 ':'')+'list pos-rel'">
		<div>
			<ul v-for="(item,key) in coinlist01" :key="key" :class="'flex-box bjs-tab-tr text-nowrap plr5'+(item.name == $store.state.marketInfo.market ? ' active':'')">
				<li class="flex-item-3">
					<a :href="$store.state.localhost+'/Trade/index/market/'+item.name" class="bjs-bi-icon"><img :src="$store.state.localhost+'/Upload/coin/'+item.img"><span class="market-base">{{ item.name.split("_")[0].toLocaleUpperCase()}}</span><span class="market-name">/{{item.name.split("_")[1].toLocaleUpperCase()}}</span></a>
				</li>
				<li class="flex-item-3 tr"><a :href="$store.state.localhost+'/Trade/index/market/'+item.name" class="db">{{ item.new_price * 1 }}</a></li>
				<!-- ,item.round *1 -->
				<li class="flex-item-3 tr">
					<a :href="$store.state.localhost+'/Trade/index/market/'+item.name" class="db"><span :class="item.change*1>0?'icon-arrow-up':'icon-arrow-down'">{{ item.change > 0 ? '+'+item.change:item.change }}%</span></a>
				</li>
				<li class="tr">
					<span :class="(!!fav[item.name] ? 'star-cur ':'')+(!!loading[item.name] ? 'loading-fav ':'')+'bjs-star'" :data-name="item.name" @click="toggleFav(item.name)"></span>
				</li>
			</ul>
		</div>
		<div v-show="morebtn" :class="'tc pad2-0'+(morebtnstate ? ' hide':'')">
			<span class="mouseover" @click="loadmore">{{ $lang.coinlistload }}</span>
		</div>
		<div v-show="more">
			<ul v-for="(item,key) in coinlist2" :key="key" :class="'flex-box bjs-tab-tr text-nowrap plr5'+(item.name == $store.state.marketInfo.market ? ' active':'')">
				<li class="flex-item-3">
					<a :href="$store.state.localhost+'/Trade/index/market/'+item.name" class="bjs-bi-icon"><img :src="$store.state.localhost+'/Upload/coin/'+item.img">{{ $store.state.language == 'zh-cn' ? item.title.replace(/\(.*\)/g,'') : item.title.replace(/.*\((.*)\/.*/,'$1').toLocaleUpperCase() }}</a>
				</li>
				<li class="flex-item-3 tr"><a :href="$store.state.localhost+'/Trade/index/market/'+item.name" class="db">{{ toFixed(item.new_price,item.round) }}</a></li>
				<li class="flex-item-3 tr">
					<a :href="$store.state.localhost+'/Trade/index/market/'+item.name" class="db"><span :class="item.change*1>0?'icon-arrow-up':'icon-arrow-down'">{{ item.change > 0 ? '+'+item.change:item.change }}%</span></a>
				</li>
				<li class="tr">
					<span :class="(!!fav[item.name] ? 'star-cur ':'')+(!!loading[item.name] ? 'loading-fav ':'')+'bjs-star'" :data-name="item.name" @click="toggleFav(item.name)"></span>
				</li>
			</ul>
		</div>
		<div v-show="coinlist.length == 0 && !$store.state.socketState" class="trade-view-loading scale5"></div>
	</div>
</template>
<script>
export default {
	name: 'List',
	//props: ['coinlist','fav','state'],
	props: {
		coinlist: {
			type: Array,
			default(){
				return []
			}
		},
		fav: {
			type: Object,
			default(){
				return {}
			}
		},
		state: {
			type: Object,
			default(){
				return {
					search: !1,
                	fav_active: !1,
				}
			}
		},
		usd: String
	},
    data () {  	
        return {
        	loading: {},
			favDate: {},
			more: false,
			morebtnstate: false
        }
	},
	computed: {
		morebtn(){
			return this.coinlist2.length > 0
		},
		coinlist01(){
			let list = []
			this.coinlistFilter.forEach(item => {
				if(item.main_coin == 0 || item.main_coin == 1){
					list.push(item)
				}
			})
			return list
		},
		coinlist2(){
			let list = []
			this.coinlistFilter.forEach(item => {
				if(item.main_coin == 2){
					list.push(item)
				}
			})
			return list
		},
		coinlistFilter(){
			let list = []
			this.coinlist.forEach(item => {
				if(!item.hide && item.name.split('_')[1]==this.usd){
					list.push(item)
				}
			})
			return list
		}
	},
    created(){
    },
    methods: {
		loadmore(){
			this.more = true
			this.morebtnstate = true
		},
		replace_market(market){
			var stateObj = { market: market };
			window.history.pushState(stateObj, 'page 2', '/market/'+market);
		},
		toFixed(num,len){
			return num.toFixed(len)
		},
    	toggleFav(coin){
    		let that = this
    		if(!that.$store.state.login){
                that.$layer.msg(that.$lang.tradeLogin,{icon: 1, time: 1000})
                return !1
            }
    		that.favDate = this.fav
    		let url = that.$store.state.servicehost+'/user/v1star/action/' + (!that.fav[coin] ? 'add' : 'remove')
    		if(that.loading[coin]){
    			return !1
    		}
    		that.$set(that.loading,coin,!0)
    		that.axios.post(url,{userId: that.$store.state.userInfo.userid, token: that.$store.state.userInfo.token, coinType: coin}).then((response) => {
			if(response.data.status == 1){
				
					that.favDate[coin] = !that.favDate[coin]
	            	that.loading[coin] = !1
					if(!that.favDate[coin] && that.state.fav_active){
						that.coinlist.forEach(elem =>{
							elem.name == coin ? elem.hide = !0 : ''
						});
					}
					that.$store.commit('setFav',that.favDate)
				}else{
					that.loading[coin] = !1
				}
	        }).catch(() => {
	        	that.loading[coin] = !1
	        });
    	}
    }
}
</script>


