<template>
    <div class="c2c-trading c2c-trading-bg clearfix font14">
	
	<span class="move"><img src="@/assets/images/move.svg"></span>
	
    	<Buy :asset="usd" :step="$store.state.marketInfo.step" :p_step="$store.state.marketInfo.p_step" @set_paypassword="set_paypassword" :setpaypassword="setpaypassword" :niceprice="$store.state.tradeNicePriceBuy" :fee="fee" />
    	<Sell :asset="coin" :step="$store.state.marketInfo.step" :p_step="$store.state.marketInfo.p_step" @set_paypassword="set_paypassword" :setpaypassword="setpaypassword" :niceprice="$store.state.tradeNicePriceSell" :fee="fee" />
    </div>
</template>

<script>
import Buy from './Buy'
import Sell from './Sell'
export default {
    name: 'Trade',
    components: {
    	Buy,
    	Sell
    },
    data () {
        return {
            setpaypassword: 2,
            fee: '0.2',
            usd: '0',
            coin: '0',
            timer: !1,
            timerNum: 0
        }
    },
    created(){
        let that = this
		
        let coinname = that.$store.state.marketInfo.market.split('_')[0]
        let market_coin = that.$store.state.marketInfo.market.split('_')[1]
        this.getAsset(coinname,market_coin)
    },
    methods: {
        getAsset(coinname,market_coin){
            let that = this
            that.timerNum ++
            if(!that.$store.state.login && that.timerNum > 6){
                clearInterval(that.timer)
                return !1
            }
            if(!that.$store.state.login){
                setTimeout(() => {
                    that.getAsset(coinname,market_coin)
                },1e3)
                return !1
            }
            that.get_paypassword()
            that.axios.get('/Pro/assets/coinname/'+coinname+'/market_coin/'+market_coin).then(response => {
                let data = response.data
                that.usd = data[market_coin]*1
                that.coin = data[coinname]
                let usd = {
                    usd: that.usd,
                    coin: that.coin
                }
                that.$store.commit('setUSD',usd)
                setTimeout(() => {
                    that.getAsset(coinname,market_coin)
                },1e4)
            })
        },
        get_paypassword(){
            let that = this
            that.axios.get('/user/tpwdsetting').then((response) => {
                let data = response.data
                that.setpaypassword = data || 2
            });
        },
        set_paypassword(){
            let that = this
            if(!that.$store.state.login){
                that.$layer.msg(that.$lang.tradeLogin,{icon: 1, time: 1000})
                return !1
            }
            that.$layer.alert({
                title: that.$lang.tradeAlert1,
                content: '<ul class="tpwd pad6"><li><label><input type="radio" name="setpaypassword" value="1"> '+that.$lang.tradeAlert2+'</label></li><li><label><input type="radio" name="setpaypassword" value="2"> '+that.$lang.tradeAlert3+'</label></li><li><label><input type="radio" name="setpaypassword" value="3"> '+that.$lang.tradeAlert4+'</label></li><li><p class="input-group trading-input-group padt10"><input type="password" placeholder="" id="setpaypassword" class="form-control form-control-tl"></p></li></ul>',
                maxWidth: 360,
                btn: that.$lang.tradeAlert6,
                yes(layero){
                    let paypassword = document.querySelector('#setpaypassword').value.trim()
                    if(paypassword == ''){
                        that.$layer.tips(that.$lang.tradeAlert5,'#setpaypassword')
                        return !1
                    }
                    that.setpaypassword = document.querySelector('[type="radio"]:checked').value
                    let data = {tpwdsetting: that.setpaypassword, paypassword: that.$md5(paypassword)}
                    that.axios.post('/user/uptpwdsetting',data).then((response) => {
                        let data = response.data
                        if(data.status == 1){
                            that.$layer.msg(data.info, {icon: 2})
                            that.$layer.close(layero)
                        }else{
                            that.$layer.msg(data.info, {icon: 3})
                        }
                    });
                },
                success(layero){
                    document.querySelector('[type="radio"][value="'+that.setpaypassword+'"]').checked = true
                }
            })
        },
    }
}
</script>



