Array.prototype.insertSort = function(fn){
    let array = this;
    fn = fn || function(a,b){
        return b > a;
    }
    for(let i = 1; i < array.length; i++){
        let key = array[i];
        let j = i - 1;
        while(j >= 0 && fn(array[j],key)){
            array[j + 1] = array[j];
            j--;
        }
        array[j + 1] = key;
    }
    return array;
}
Object.defineProperty(Array.prototype, 'insertSort', {'enumerable': false}); 

export default Array


