<template>
    <div class="my-tab-scroll">
        <div class="my-tab-scroll-head new-list">
            <ul class="flex-box new-list-item new-list-item-head tc text-nowrap hover-ova-pad tophgrey">
                <li class="flex-item-2">{{ $lang.assetTime2 }}</li>
                <li>{{ $lang.assetBuySell }}</li>
                <li class="flex-item-2">{{ $lang.coinlistPrice }}</li>
                <li class="flex-item-2">{{ $lang.assetVolume }}</li>
                <li class="flex-item-2">{{ $lang.tradeFee }}</li>
                <li class="flex-item-2">{{ $lang.assetAmount }}</li>
            </ul>
        </div>
        <div id="my-tab-scroll-box-0">
            <div class="new-list">
                <ul v-for="(item,key) in datalist" :class="'flex-box new-list-item tc text-nowrap hover-ova-pad'+(key%2==0?' odd':' even')" :key="key">
                    <li class="flex-item-2">{{ item.time }}</li>
                    <li v-if="item.type == 1"><span class="bjs-color-up">{{ $lang.tradeBuy }}</span></li>
                    <li v-if="item.type == 2"><span class="bjs-color-down">{{ $lang.tradeSell }}</span></li>
                    <li class="flex-item-2">{{ item.price }}</li>
                    <li class="flex-item-2">{{ item.volume }}</li>
                    <li class="flex-item-2">{{ item.fee }}</li>
                    <li class="flex-item-2">{{ item.amount }}</li>
                </ul>
                <div v-show="!datalist.length" class="no-data tc padt10">
                    <span>{{ $lang.assetNotData }}</span>
                </div>
                <div v-show="datalist.length >= 20" class="tc padt10">
                    <a :href="$store.state.localhost+'/User/trade_log'" target="_blank">{{ $lang.viewMore }}</a>
                    <p class="padt10"></p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Traded',
    props: {
        datalist: {
            type: Array,
            default(){
                return []
            }
        }
    },
    data () {
        return {}
    }
}
</script>



