<template>
    <div id="tradingview_e47e3" class="c2c-tradingview">
    </div>
</template>

<script>
import { widget as TvWidget } from '../../../../static/tradeview/charting_library/charting_library.min.js'
import socket from './datafeeds/socket.js'
import datafeeds from './datafeeds/datafees.js'
import Dafeed from './datafeeds/Dafeed.js'
import config from './config.js'
import update from '../../../global/update.js'
export default {
    props: {
        symbol: {
            type: String,
            default: 'BTCUSD'
        },
        initMarket: {
            type: Boolean,
            default: !1
        }
    },
    data() {
        return {
            widget: null,
            socket: null,
            datafeeds: new datafeeds(this),
            config: new config(this),
            interval: null,
            cacheData: {},
            cacheDataHistory: {},
            lastTime: null,
            getUserInfoNum: 0,
            getBarTimer: null,
            isLoading: true
        }
    },
    created() {
	
        this.loading = !1
        this.$store.commit('addTradingviewConfig', this.config)
        this.interval = localStorage.getItem('tradingview.resolution') || '5';
        this.$store.commit('setUpdate', new update(this))
        
		this.init(this.symbol, this.interval)
		if(this.initMarket){ 
		
            let url = this.$store.state.marketInfo.ws
            this.socket = new socket(url)   
            this.socket.doOpen()        
            this.socket.on('open', () => {
                this.socket.send({ type: 'login', symbol: this.symbol})
            })
            this.socket.on('message', this.onMessage)
            this.socket.on('close', this.onClose)
            this.runTimeUpdate()
            let interval = localStorage.getItem('tradingview.resolution') || '5'
            this.init(this.symbol, interval)
        }
    },
    watch: {
        initMarketx(){
            let url = this.$store.state.marketInfo.ws
            this.socket = new socket(url)
            this.socket.doOpen()
            this.socket.on('open', () => {
                this.socket.send({ type: 'login', symbol: this.symbol})
            })
            this.socket.on('message', this.onMessage)
            this.socket.on('close', this.onClose)
            this.runTimeUpdate()
            let interval = localStorage.getItem('tradingview.resolution') || '5'
            this.init(this.symbol, interval)
        }
    },
    methods: {
        init(symbol = 'BTCUSDT', interval = '5') {
            let skin = this.$store.state.skin
            let language = this.$store.state.language.split('-')[0]
            let overrides = this.config.getOverrides(skin)
            let studies_overrides = this.config.getStudiesOverrides(skin)
            
        },
        onChartReady(skin){
            let that = this
            that.config.loading = !0
            that.config.createStudy();
            that.config.createButton(that.config.buttons);
            that.widget.chart().setChartType(that.config.chartType);
            that.config.toggleStudy(that.config.chartType);
            if(skin != that.$store.state.skin){
                skin = that.$store.state.skin
            }
            setTimeout(() => {
                that.config.resetTheme(skin)
            },0)
            setTimeout(() => {
                that.$emit('loadingView',1)
            },0)
        },
        sendMessage(data) {
            if (this.socket.checkOpen()) {
                this.socket.send(data)
            } else {
                this.socket.on('open', () => {
                this.socket.send(data)
                })
            }
        },
        runTimeUpdate(){
            let date = new Date()
            let s = date.getSeconds()
            let ms = date.getMilliseconds()
            //console.log('>> runTime',60*1e3-s*1e3-ms)
            let st = 0
            setTimeout(() => {
                this.runtime()                
            },60*1e3-s*1e3-ms)
        },
        runtime(){
            if(this.lastTime){
                let nowTime = parseInt(new Date().getTime()/10)*10
                let lastTime = this.lastTime
                let interval = 1000*60
                if(this.interval <= 60){
                    interval = 1000*60*this.interval
                }else if(this.interval == 'D' || this.interval == '1D'){
                    interval = 1000*60*60*24
                }else if(this.interval == 'W' || this.interval =='1W'){
                    interval = 1000*60*60*24*7
                }else if(this.interval == 'M' || this.interval == '1M'){
                    interval = 1000*60*60*24*30
                }
                if(lastTime && nowTime - lastTime >= interval){
                    //console.log('>> update')
                    const ticker = `${this.symbol}-${this.interval}`
                    let lastData = this.cacheData[ticker][this.cacheData[ticker].length - 1]
                    //console.log('>> lastData: ',lastData)
                    const barsData = {
                        time: lastTime+interval,
                        open: lastData.close,
                        high: lastData.close,
                        low: lastData.close,
                        close: lastData.close,
                        volume: 0
                    }
                    this.cacheData[ticker].push(barsData)
                    this.lastTime = barsData.time
                    this.datafeeds.barsUpdater.updateData()
                }
            }
            this.runTimeUpdate()
        },
        getHistory(from,to){
            let resolution = this.interval
            if(this.interval == 'W' || this.interval == '1W' || this.interval == 'M' || this.interval == '1M'){
                resolution = '1D'
            }
            this.cacheData['from'] = from
            this.sendMessage({ type: 'kline_query', resolution: resolution, from: from, to: to})
        },
        getSubscribe(){
            let resolution = this.interval
            if(this.interval == 'W' || this.interval == '1W' || this.interval == 'M' || this.interval == '1M'){
                resolution = '1D'
            }
            this.sendMessage({ type: 'kline_sub', resolution: resolution })
        },
        getCoinList(){
            this.sendMessage({ type: 'coinlist' })
        },
        getExchange(){
            this.sendMessage({ type: 'exchange' })
        },
        getEntrust(){
            let resolution = this.$store.state.entrustDepth
            this.sendMessage({ type: 'entrust', resolution: resolution })
        },
        getDepth(){
            this.sendMessage({ type: 'depth' })
        },
        getUserInfo(){
            this.getUserInfoNum ++
            if(this.getUserInfoNum > 10){
                return !1
            }
            if(this.$store.state.userInfo.userid != ''){
                this.sendMessage({ type: 'userInfo', userId: this.$store.state.userInfo.userid, token: this.$store.state.userInfo.token })
            }else{
                setTimeout(() => {
                    this.getUserInfo()
                }, 1e3)
            }            
        },
        loginInit(){
            let form = Date.now()
            if (this.interval <= 60) {
                form -= 10*24*60*60*1000
            } else {
                form -= 1440*24*60*60*1000
            }
            //Incremental update
            this.getSubscribe()
            //historical data
            this.getHistory(form, Date.now())
            //Market
            this.getCoinList()
            //Latest deal
            this.getExchange()
            //entrust
            this.getEntrust()
            //Depth
            this.getDepth()
            //UserData
            this.getUserInfo()
        },
        onMessage(data) {
            if(data.r == 'login' && data.s){
                this.$store.commit('changeSocketState',!0)
                this.loginInit()                
                return !1
            }
            // console.log('History Records')
            if (data.r == 'kline_query' && data.s && data.t.length) {
                const list = []
                const ticker = `${this.symbol}-${this.interval}`
                let step = 0
                if(this.interval <= 60){
                    step = this.interval*60*1000
                } else {
                    step = 24*60*60*1000
                }
                let index = 0
                for(let time = data.t[0]; time <= data.to; time+=step){
                    let nodata = false
                    if(time < data.t[index] || data.t[index] == undefined){
                        nodata = true
                        index --
                    }
                    list.push({
                        time: time,
                        open: nodata ? data.c[index] : data.o[index],
                        high: nodata ? data.c[index] : data.h[index],
                        low: nodata ? data.c[index] : data.l[index],
                        close: data.c[index],
                        volume: nodata ? 0 : data.v[index]
                    })
                    index ++
                }
                if(!list.length){
                    this.cacheData[ticker+'noData'] = true
                    return !1
                }
                //console.log('>> list: ',list)
                if(!this.cacheData[ticker]){
                    this.cacheData[ticker] = list
                    this.lastTime = list[list.length - 1].time
                    //this.subscribe()
                }else{
                    this.cacheDataHistory[ticker] = list
                }                
            }
            if (data.r == 'kline_query' && data.s && !data.t.length) {
                //noData
                const ticker = `${this.symbol}-${this.interval}`
                this.cacheData[ticker+'noData'] = true
            }
            // console.log('>> Incremental update: ',this.lastTime,!!this.lastTime)
            if (data.r == 'kline_sub' && data.s && this.lastTime) {
                // Edit title
                document.title = (data.c*1) + ' ' + this.symbol.toLocaleUpperCase().replace(/_/,'/') + 'T '
                //console.log('data',data)
                //Notification chart update data
                const ticker = `${this.symbol}-${this.interval}`
                const barsData = {
                    time: data.t,
                    open: data.o,
                    high: data.h,
                    low: data.l,
                    close: data.c,
                    volume: data.v
                }
                if (barsData.time > this.lastTime && this.cacheData[ticker] && this.cacheData[ticker].length) {
                    //console.log('>> barsData add: ',barsData)
                    this.cacheData[ticker].push(barsData)
                    this.lastTime = barsData.time
                }else if(barsData.time == this.lastTime && this.cacheData[ticker] && this.cacheData[ticker].length){
                    //console.log('>> barsData replace: ',barsData)
                    this.cacheData[ticker][this.cacheData[ticker].length - 1] = barsData
                }
                this.datafeeds.barsUpdater.updateData()
            }else if(data.r == 'kline_sub' && data.s && !this.lastTime){
                document.title = (data.c*1) + ' ' + this.symbol.toLocaleUpperCase().replace(/_/,'/') + 'T '
                const ticker = `${this.symbol}-${this.interval}`
                const barsData = {
                    time: data.t,
                    open: data.o,
                    high: data.h,
                    low: data.l,
                    close: data.c,
                    volume: data.v
                }
                this.cacheData[ticker] = [barsData]
                this.lastTime = barsData.time
                this.datafeeds.barsUpdater.updateData()
            }

            //Update data - global
            this.$store.state.update.onMessage(data)
        },
        onClose(){
            let that = this
            //console.log('>> : Closed')
            that.socket.doOpen()
            that.socket.on('open', () => {
                //console.log('>> : Reconnected')
                that.socket.send({ type: 'login', symbol: that.symbol})
            });
        },
        getBars(symbolInfo, resolution, rangeStartDate, rangeEndDate, onLoadedCallback) {
            //console.log(' >> :', rangeStartDate, rangeEndDate)
            resolution = localStorage.getItem('tradingview.resolution') || 5
            //Time particles can only be obtained through tradingview.resolution, because the time of getBars is only up to days, but our plugin can go to the month.
            //console.log(' >> :', this.interval, resolution)
            if (this.interval !== resolution) {
                this.interval = resolution
                const ticker = `${this.symbol}-${this.interval}`
                const tickerold = `${this.symbol}-${resolution}`
                this.lastTime = null
                this.cacheData[tickerold] = null
                this.cacheDataHistory[tickerold] = null
                this.cacheData[tickerold+'noData'] = false
                this.getHistory(rangeStartDate*1000, (rangeEndDate-60)*1000)
                this.isLoading = true
                this.getSubscribe()
            }
            const ticker = `${this.symbol}-${this.interval}`
            if (this.cacheData[ticker] && this.cacheData[ticker].length) {
                const newBars = []
                //If the history cache exists, first get the history cache
                if(this.cacheDataHistory[ticker] && this.cacheDataHistory[ticker].length){
                    this.cacheDataHistory[ticker].forEach(item => {
                        if (item.time >= rangeStartDate * 1000 && item.time <= rangeEndDate * 1000) {
                            newBars.push(item)
                        }
                    })
                }
                //Get it from the latest cache
                this.cacheData[ticker].forEach(item => {
                    if (item.time >= rangeStartDate * 1000 && item.time <= rangeEndDate * 1000) {
                        newBars.push(item)
                    }
                })
                //If the required data is outside the history cache, re-acquire, such as the current need [100,300], the history cache data is [200,-], re-acquire [100,200], hold
                if(this.cacheData['from'] > rangeStartDate*1000){
                    this.getHistory(rangeStartDate*1000, this.cacheData['from'])
                    const self = this
                    this.getBarTimer = setTimeout(function () {
                        self.getBars(symbolInfo, resolution, rangeStartDate, rangeEndDate, onLoadedCallback)
                    }, 50)
                    return !1
                }
                if(!newBars.length){
                    if(!this.isLoading){
                        this.cacheData[ticker+'noData'] = false
                        this.getHistory(rangeStartDate*1000, rangeEndDate*1000)
                        this.isLoading = true
                    }else if(this.cacheData[ticker+'noData']){
                        onLoadedCallback({noData: true})
                        return !1
                    }
                    const self = this
                    this.getBarTimer = setTimeout(function () {
                        self.getBars(symbolInfo, resolution, rangeStartDate, rangeEndDate, onLoadedCallback)
                    }, 50)
                }else{
                    this.isLoading = false
                    onLoadedCallback(newBars)
                }
            } else {
                const self = this
                if(this.cacheData[ticker+'noData']){
                    onLoadedCallback({noData: true})
                    return !1
                }
                this.getBarTimer = setTimeout(function () {
                    self.getBars(symbolInfo, resolution, rangeStartDate, rangeEndDate, onLoadedCallback)
                }, 10)
            }
        }
    }
}
</script>




