<template>
    <div class="my-tab-box trade-unit-box clearfix">
	<span class="move"><img src="@/assets/images/move.svg"></span>
        <div class="my-tab-cont">
            <div class="my-tab-scroll-wrap-head pos-rel tophgrey">
                <div class="c2c-table-box-head">
                    <ul class="flex-box new-list-item-head pad6 hover-ova-pad">
                        <li class="tl">{{ $lang.assetTime }}</li>
                        <li class="tc">{{ $lang.latestdealPrice }}</li>
                        <li class="tr">{{ $lang.latestdealVolume }}</li>
                    </ul>
                </div>
            </div>
            <div class="my-tab-scroll-wrap-box pos-rel overflow-auto ovh">
                <List :list="$store.state.socketLatestdeal" />
            </div>
        </div>
    </div>
</template>

<script>
import List from './List'
export default {
    name: 'Latestdeal',
    components: {
        List
    },
    data () {
        return {}
    }
}
</script>



