<template>
    <div class="my-tab-box trade-unit-box clearfix">
	<span class="move"><img src="@/assets/images/move.svg"></span>
        <div class="my-tab-cont latestdeal-depth-show">
            
			<div class="my-tab-scroll-wrap-head pos-rel tophgrey">
			
                <div class="c2c-table-box-head">
                    <ul class="flex-box new-list-item-head pad6 hover-ova-pad">
                        <li>{{ $lang.coinlistPrice }}({{ $store.state.marketInfo.market.toLocaleUpperCase().split('_')[1] }})</li>
                        <li class="tc">{{ $lang.assetVolume }}({{ $store.state.marketInfo.market.toLocaleUpperCase().split('_')[0] }})</li>
                        <li class="tr">{{ $lang.depthAmount }}({{ $store.state.marketInfo.market.toLocaleUpperCase().split('_')[1] }})</li>
                    </ul>
                </div>
            </div>
            <div class="my-tab-scroll-wrap-box pos-rel">
                <div :class="'my-tab-scroll-cont my-tab-scroll-cont4 ovh'+(showlist==1?'':showlist==2?' show-all-height':' hide')">
                    <Sell :list="$store.state.socketPendingOrderSell" />
                </div>
                <div :class="'my-tab-scroll-cont4-bob'+(showlist==1?'':showlist==2?' show-all-bottom':' show-all-top')">
                    <em @click="setPrice" class="bjs-color-up font18 pl5 mouseover">{{ (nicePrice*1).toFixed($store.state.marketInfo.p_round) }}</em>
                     <!-- {{ tousdt }} -->
                </div>
                <div :class="'my-tab-scroll-cont my-tab-scroll-cont6 ovh'+(showlist==1?'':showlist==3?' show-all-height':' hide')">
                    <Buy :list="$store.state.socketPendingOrderBuy" />
                </div>
            </div>
        </div>
        <div class="pr15 mat5">
            <div ref="select_box" id="select_box" class="depth-select pl5 dblb">
                <span class="mouseover" @click="selectShow">{{ $lang.latestdealDepth }} <span :class="'depth-select-arr'+(depthSelect?' active':'')">{{ depth }}</span></span>
                <ul :class="'depth-select-list tr'+(depthSelect?'':' hide')+(rect?' bottom':'')">
                    <li v-for="(item,key) in depthlist" @click.prevent.stop="select(item)" :class="'depth-select-option mouseover'+(depth == item ? ' active':'')" :key="key">{{ item }}</li>
                </ul>
            </div>
            <div class="pull-right">
                <span @click="changeBox(1)" :class="'mouseover dblb pending-order-all pending-order-btn'+(showlist==1?' active':'')"></span>
                <span @click="changeBox(2)" :class="'mouseover dblb pending-order-sell pending-order-btn'+(showlist==2?' active':'')"></span>
                <span @click="changeBox(3)" :class="'mouseover dblb pending-order-buy pending-order-btn'+(showlist==3?' active':'')"></span>
            </div>
        </div>
    </div>
</template>

<script>
import Sell from './Sell'
import Buy from './Buy'
export default {
    name: 'Pendingorder',
    props: ['p_round','nicePrice'],
    components: {
        Sell,
        Buy
    },
    data () {
        return {
            depthSelect: !1,
            depth: 0.01,
            depthlist: [],
            showlist: 1,
            rect: !0
        }
    },
    created(){
        this.createStep(this.p_round)
    },
    computed: {
        tousdt(){
            console.log(this.$store.state.marketInfo);
            let price = '';
                price = ' $'+(this.nicePrice * this.$store.state.marketInfo.tousdt).toFixed(this.p_round);
            return price;
        }
    },
    watch: {
        p_round(){
            this.createStep(this.p_round)
        }
    },
    methods: {
        createStep(round){
            let list = [
                0.000001,
                0.00001,
                0.0001,
                0.001,
                0.01,
                0.1,
                1,
                10,
                100,
                1000,
                10000,
                100000,
                1000000,
            ]
            let depth = list[6-round]
            this.depth = depth
            this.depthlist = list.slice(6-round,12-round)
            this.$store.commit('changeSocketEntrustDepth',round)
        },
        setPrice(){
            this.$store.commit('setTradePriceBuy',this.nicePrice)
            this.$store.commit('setTradePriceSell',this.nicePrice)
        },
        selectShow(){
            if(!this.depthSelect){                
                setTimeout(() => {
                    document.addEventListener('keydown',this.closeSelect,1)
                    document.addEventListener('click',this.listenSelect,!1)
                },100)
                let top = this.$refs.select_box.getBoundingClientRect().top - window.innerHeight
                if(top > -173){
                    this.rect = !0
                }else{
                    this.rect = !1
                }
            }
            this.depthSelect = !this.depthSelect            
        },
        select(depth){
            document.removeEventListener('keydown',this.closeSelect,1)
            document.removeEventListener('click',this.listenSelect,!1)
            if(this.depth == depth){
                this.depthSelect = !1
                return !1
            }
            this.depth = depth
            this.depthSelect = !1
            let list = [
                0.000001,
                0.00001,
                0.0001,
                0.001,
                0.01,
                0.1,
                1,
                10,
                100,
                1000,
                10000,
                100000,
                1000000,
            ]
            let resolution = 2
            for(let i = 0; i < list.length; i++){
                if(depth == list[i]){
                    resolution = 6-i
                }
            }
            //this.$store.commit('changeSocketEntrustDepth',resolution)
            this.$store.state.update.sendMessage({type: "entrust", resolution: resolution })
        },
        closeSelect(event){
            if(event.keyCode == 27){
                this.depthSelect = !this.depthSelect
                document.removeEventListener('keydown',this.closeSelect,1)
                document.removeEventListener('click',this.listenSelect,!1)
            }
        },
        listenSelect(){
            if(this.depthSelect){
                this.depthSelect = !this.depthSelect
            }
            document.removeEventListener('click',this.listenSelect,!1)
        },
        changeBox(value){
            let that = this
            that.showlist = value
        }
    }
}
</script>



