<template>
    <div class="my-tab-box trade-unit-box clearfix">
		<h3 class="trade-view-title tophdark">
			<ul class="flex-box">
	            <li class="flex-item-8 text-overflow">
	                <span class="trade-bi-icon">
	                	<img v-if="$store.state.marketInfo.img" :src="$store.state.localhost+'/Upload/coin/'+$store.state.marketInfo.img"><span>{{ $store.state.language == 'zh-cn' ? $store.state.marketInfo.title : $store.state.marketInfo.title.replace(/(.*)\((.*)\//g,'$2($2/') }}</span>
	                </span>
					<span v-if="$store.state.coinlist[$store.state.marketInfo.market] && $store.state.coinlist[$store.state.marketInfo.market].main_coin == 1" class="dblb trade-bi-icon-area lh1375 font12">{{ $lang.coinlistMain }}</span>
					<span v-if="$store.state.coinlist[$store.state.marketInfo.market] && $store.state.coinlist[$store.state.marketInfo.market].main_coin == 0" class="dblb trade-bi-icon-area lh1375 font12">{{ $lang.coinlistInnovation }}</span>
					<span v-if="$store.state.coinlist[$store.state.marketInfo.market] && $store.state.coinlist[$store.state.marketInfo.market].main_coin == 2" class="dblb trade-bi-icon-area lh1375 font12">{{ $lang.coinlistExperiment }}</span>
	            </li>
	            <li class="flex-item-2 text-nowrap">
	                <div class="tr pr3-important">
						<span :class="(tabs.asset == 1 ? 'c00ffff ':'')+'mouseover darkgray'" @click="tabChange(1)">{{ $lang.kline }}</span>
						<!-- <span :class="(tabs.asset == 2 ? 'c00ffff ':'')+'mouseover ml5 darkgray'" @click="tabChange(2)">{{ $lang.assetDepth }}</span>-->
						<span class="move"><img src="@/assets/images/move.svg"></span> 
	                </div>
	            </li>
	        </ul>
		</h3>
		<div class="trade-view-box" v-show="tabs.asset == 1">
		<iframe style="width:100%; height:500px; border:none;" :src="$store.state.localhost+'/Trade/tradingview/?skin='+$store.state.skin+'&market='+$store.state.marketInfo.market"/>
			<div class="hide"><trade-view ref="trade" :symbol="symbol" :initMarket="$store.state.initMarket" @loadingView="loadingView"></trade-view></div>
		</div>
		<div class="c2c-tradingview" v-show="tabs.asset == 2">
			<Depth :show="tabs.asset" :datalist="$store.state.socketDepth" :skin="$store.state.skin" :resize="$store.state.resize" />
		</div>
	</div>
</template>

<script>
import TradeView from '../TradeView/Index'
import Depth from './Depth'
export default {
    name: 'Kline',
	components: {
		TradeView,
		Depth
	},
    data () {
        return {
			loading: !1,
        	symbol: this.$store.state.marketInfo.market,
        	tabs: {
        		asset: 1
        	}
        }
	},
	methods: {
		loadingView(value){
			this.loading = value
		},
		tabChange(asset){
            if(asset == this.tabs.asset){
                return !1
            }
            this.tabs.asset = asset
        }
	}
}
</script>


