<template>
    <div class="my-tab-scroll">
        <div class="my-tab-scroll-head new-list">
            <ul class="flex-box new-list-item new-list-item-head tophgrey tc text-nowrap hover-ova-pad">
                <li class="flex-item-2">{{ $lang.assetTime1 }}</li>
                <li>{{ $lang.assetBuySell }}</li>
				<li>Condition</li>
				<li>Stop</li>
				<li>Trade Type</li>
                <li class="flex-item-2">{{ $lang.coinlistPrice }}</li>
                <li class="flex-item-2">{{ $lang.assetVolume }}</li>
                <li class="flex-item-2">{{ $lang.assetDeal }}</li>
                <li class="flex-item-2">{{ $lang.assetAmount }}</li>
                <li class="flex-item-2">{{ $lang.assetOperation }}</li>
            </ul>
        </div>
        <div id="my-tab-scroll-box-1">
            <div class="new-list">
                <ul v-for="(item,key) in datalist" :class="'flex-box new-list-item tc text-nowrap hover-ova-pad'+(key%2==0?' odd':' even')" :key="key">
                    <li class="flex-item-2">{{ item.time }}</li>
                    <li ><span class="bjs-color-up" v-if="item.type == 1">{{ $lang.tradeBuy }}</span><span class="bjs-color-down" v-if="item.type == 2">{{ $lang.tradeSell }}</span></li>
					<li >{{ item.condition }}</li>
					<li >{{ item.stop }}</li>
					<li >{{ item.tradetype }}</li>
                    <li class="flex-item-2">{{ item.price }}</li>
                    <li class="flex-item-2">{{ item.volume }}</li>
                    <li class="flex-item-2">{{ item.deal }}</li>
                    <li class="flex-item-2">{{ item.amount }}</li>
                    <li class="flex-item-2">
                        <a href="javascript:;" class="bjs-btn bjs-btn-cyan" @click="cancel(item.id,item.tradetype)">{{ $lang.assetCancel }}</a>
                    </li>
                </ul>
                <div v-show="!datalist.length" class="no-data tc padt10">
                    <span>{{ $lang.assetNotData }}</span>
                </div>
                <div v-show="datalist.length >= 20" class="tc padt10">
                    <a :href="$store.state.localhost+'/Finance/mywt'" target="_blank">{{ $lang.viewMore }}</a>
                    <p class="padt10"></p>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: 'Entrust',
    props: {
        datalist: {
            type: Array,
            default(){
                return []
            }
        }
    },
    created(){
        this.init()
    },
    data () {
        return {
            coin: this.$store.state.marketInfo.market.split('_')[0],
            ajaxnum: 0
        }
    },
    methods: {
        init(){
            let that = this
            that.ajaxnum ++
            if(!that.$store.state.login && that.ajaxnum > 20){
                return !1
            }
            if(!that.$store.state.login){
                setTimeout(() => {
                   that.init() 
                },1e3)
                return !1
            }
            let round = this.$store.state.marketInfo.round
            let p_round = this.$store.state.marketInfo.p_round
            that.axios.get('/Pro/getEntrustAndUsercoin?market='+that.$store.state.marketInfo.market).then(response => {
                let entrustList = [];
                let entrust = response.data.entrust
                if(entrust && entrust.length){
                    for(let i = 0; i < entrust.length; i++){
                        let elem = entrust[i]
                        let obj = {
                            id: elem.id,
                            time: elem.addtime,
                            deal: elem.deal,
                            type: elem.type,
							condition: elem.condition,
							stop: elem.stop,
							tradetype: elem.tradetype,
                            price: (elem.price*1).toFixed(p_round),
                            volume: (elem.num*1).toFixed(round),
                            amount: (elem.price*elem.num).toFixed(p_round)
                        }
                        entrustList.push(obj)
                    }
                }

                let tradelogList = []
                let tradelog = response.data.tradelog
                if(tradelog && tradelog.length){
                    for(let i = 0; i < tradelog.length; i++){
                        let elem = tradelog[i]
                        let obj = {
                            id: elem.id,
                            time: elem.addtime,
                            fee: elem.fee*1,
                            type: elem.type,
                            price: (elem.price*1).toFixed(p_round),
                            volume: (elem.num*1).toFixed(round),
                            amount: (elem.price*elem.num).toFixed(p_round)
                        }
                        tradelogList.push(obj)
                    }
                }

                that.$store.commit('changeSocketAsset',entrustList)
                that.$store.commit('changeTradeLog',tradelogList)
                setTimeout(() => {
                    that.init()
                },1e4)
            })
        },
        cancel(id,type){            
		var router='reject';
	if(type=='limit'){
	 router='reject'
	}
	if(type=='Stop-Limit'){
		router='stopreject'
    }
	
            let that = this
            that.$layer.alert({
                content: that.$lang.assetCancel2,
				btn: 'Confirm',
				title: 'Cancle Order',
                yes(layero){
                    that.$layer.close(layero)
                    layero = that.$layer.loading()
                    that.axios.post("/Trade/"+router+"", {id: id} ).then(response => {
                        let data = response.data
                        this.state.trade.created()
                        if(data.status == 1){
                            let datalist = that.$store.state.socketAsset
                            let index = -1
                            for(let i = 0; i < datalist.length; i++){
                                if(datalist[i].id == id){
                                    index = i
                                }
                            }
                            if(index > -1){
                                datalist.splice(index,1)
                                that.$store.commit('changeSocketAsset',datalist)
                            }
                            that.$layer.msg(data.info,{icon: 2})
                        }else{
                            that.$layer.msg(data.error,{icon: 3})
                        }
                        that.$layer.close(layero)                        
                    }).catch(() => {
                        that.$layer.close(layero)
                    })
                }
            })
        },
        myzc(){
            let that = this
            let coin = that.$store.state.marketInfo.market.split('_')[0]
            let localhost = that.$store.state.localhost
            let href = localhost+'/Finance/myzc/coin/'+coin
            window.location.href = href
        }
    }
}
</script>



