<template>
    <div class="new-list pending-buy-box">
        <ul v-for="(item,key) in list" :title="$lang.pengingorderSellTitle" @click="setPrice('Sell',item.price,depth[key])" :class="'flex-box depth-rect pad0-6 mouseover hover-ova-pad'+(newsdepth[key] && change ? ' animate-depth-up'+animteChange:'')" :key="key">
            <li>
                <div class="depth-rect-up" :style="'width: '+(depth[key]/sum*100)+'%;'"></div>
                <div class="bjs-color-up">{{ item.price * 1 }}</div>
            </li>
            <li class="tc">{{ item.volume * 1 }}</li>
            <li class="tr">{{ (item.volume * item.price * 1).toFixed(2) }}</li>
        </ul>
        <ul v-for="(item,key) in listadd" :class="'flex-box depth-rect pad0-6 hover-ova-pad'" :key="'add'+key">
            <li>
                <div class="opacity6">---</div>
            </li>
            <li class="tc"><div class="opacity6">---</div></li>
            <li class="tr"><div class="opacity6">---</div></li>
        </ul>
        <div v-show="list.length == 0 && !$store.state.socketState" class="trade-view-loading scale5"></div>
    </div>
</template>

<script>
export default {
    name: 'Buy',
    props: {
        list: {
            type: Array, 
            default(){
                return []
            }
        }
    },
    data () {
        return {
            depth: [],
            listbak: [],
            listadd: [],
            newsdepth: [],
            change: !0,
            entrustDepth: this.$store.state.entrustDepth,
            animteChange: '',
            sum: 0
        }
    },
    watch: {
        list(){
            if(this.$store.state.entrustDepth != this.entrustDepth){
                this.entrustDepth = this.$store.state.entrustDepth
                this.change = !1
            }else{
                this.change = !0
            }
            this.listdata = this.list
            let add = 30 - this.list.length
            for(let addi = 0; addi < add; addi++){
                this.listadd.push(1)
            }
            //Save the value of each record -- the string
            let bak = []
            //Save whether each record changes
            let news = []
            //Total value of the depth map
            this.sum = 0
            this.list.forEach((elem) => {
                this.sum += elem.volume*1
                //sum += elem.volume*1
                bak.push(JSON.stringify(elem))
            })
            if(this.listbak.length){
                for(let i = 0; i < bak.length; i++){
                    let temp = bak[i]
                    let tempStatus = 1
                    for(let j = 0; j < this.listbak.length; j++){
                        //If the comparison finds the same value in the list, the record is not updated.
                        if(temp == this.listbak[j]){
                            tempStatus = 0
                            break
                        }
                    }
                    news.push(tempStatus)
                }
            }
            if(this.animteChange == ''){
                this.animteChange = 1
            }else{
                this.animteChange = ''
            }
            this.listbak = bak
            this.newsdepth = []
            setTimeout(() => {
                this.newsdepth = news
            },20)
            //this.newsdepth = news

            //Clear depth
            this.depth = [];
            let volume = 0
            this.list.forEach((elem) => {
                volume += elem.volume*1
                //Recalculate depth
                this.depth.push((volume).toFixed(this.$store.state.marketInfo.round))
            })
            //console.log('>> Buy',this.listdata)
        }
    },
    methods: {
        setPrice(type,price,volume){
            this.$store.commit('setTradePrice',price)
            let _volume = volume
            //Sell
            this.$store.commit('setTradePrice',price)
            if(volume > this.$store.state.usd.coin){
                volume = this.$store.state.usd.coin
                if(volume == 0){
                    volume = ''
                }
            }
            this.$store.commit('setTradeVolumeSell',volume)
            //Buy
            if(_volume * price > this.$store.state.usd.usd){
                _volume = this.$store.state.usd.usd / price / 1.002
                if(_volume == 0){
                    _volume = ''
                }else{
                    _volume = Math.toFixed(_volume,this.$store.state.marketInfo.round)
                }
            }
            this.$store.commit('setTradeVolumeBuy',_volume)
            this.initMarket(price)
        },
        initMarket(price){
            let that = this
            let data = that.$store.state.marketInfo
			console.log('buyvue134');
            if(price.split('.')[1]){
                data.p_round = price.split('.')[1].length
            }else{
                data.p_round = 0
            }
            data.round = 8 - data.p_round
            let step = '1'
            if(data.round && data.round > 1){
                step = '0.'
                for(let i = 1; i < data.round; i++){
                    step += '0'
                }
                step += '1';
            }
            data.step = step
            let p_step = '1'
            if(data.p_round && data.p_round > 1){
                p_step = '0.'
                for(let i = 1; i < data.p_round; i++){
                    p_step += '0'
                }
                p_step += '1';
            }
            data.p_step = p_step
            that.$store.commit('setMarketInfo',data)
        }
    }
}
</script>


