<template>
    <div class="content">
        <Submenu />
        <Stats />
        <div
            :class="
                (layout ? 'layout-off ' : '') +
                    'clearfix trade-page-content trade-page layout-animate'
            "
        >
            <dnd-grid-container
                :layout.sync="layoutJson"
                :cellSize="cellSize"
                :maxColumnCount="maxColumnCount"
                :maxRowCount="maxRowCount"
                :margin="margin"
                :bubbleUp="bubbleUp"
            >
                <dnd-grid-box boxId="coinlistBox" dragSelector="span.move img">
                    <div class=" roundbox">
                        <Coinlist
                            :fav="$store.state.fav"
                            :coinlistdata="$store.state.socketCoinList"
                        />
                    </div>
                </dnd-grid-box>

                <dnd-grid-box boxId="klinebox" dragSelector="span.move img">
                    <div class="page-left-2-1 roundbox klinebox">
                        <Kline />
                    </div>
                </dnd-grid-box>
                <dnd-grid-box boxId="tradesbox" dragSelector="span.move img">
                    <div class="page-left-2-2 roundbox tradesbox">
                        <Trade />
                    </div>
                </dnd-grid-box>

                <dnd-grid-box boxId="lastpriceBox" dragSelector="span.move img">
                    <div class="roundbox">
                        <ul
                            :class="
                                status == 0
                                    ? 'flex-box movebox tophdark'
                                    : 'flex-box-2 clearfix movebox tophdark'
                            "
                        >
                            <li>
                                <span
                                    @click="checkTab(1)"
                                    :class="
                                        (status == 1 ? 'mouseover' : '') +
                                            (status == 1 && tab == 1
                                                ? ' active'
                                                : '') +
                                            ' item-new-btn darkgray'
                                    "
                                    >{{ $lang.pengingorderNew }}</span
                                >
                            </li>
                            <li :class="status == 0 ? 'bl-2' : ''">
                                <span
                                    @click="checkTab(2)"
                                    :class="
                                        (status == 1 ? 'mouseover' : '') +
                                            (status == 1 && tab == 2
                                                ? ' active'
                                                : '') +
                                            ' item-new-btn darkgray'
                                    "
                                    >{{ $lang.latestdealLastTrade }}</span
                                >
                            </li>
                        </ul>
                        <ul
                            :class="
                                status == 0 ? 'flex-box movebox tophdark' : ''
                            "
                        >
                            <li v-show="status == 0 || tab == 1">
                                <div
                                    :class="
                                        'page-left-5' +
                                            (status == 1 && tab == 1
                                                ? ' w100'
                                                : '')
                                    "
                                >
                                    <Pendingorder
                                        :nicePrice="$store.state.tradeNicePrice"
                                        :p_round="
                                            $store.state.entrustDepthRound
                                        "
                                    />
                                </div>
                            </li>
                            <li
                                v-show="status == 0 || tab == 2"
                                :class="status == 0 ? 'bl-2' : ''"
                            >
                                <div
                                    :class="
                                        'page-left-5' +
                                            (status == 1 && tab == 2
                                                ? ' w100'
                                                : '')
                                    "
                                >
                                    <Latestdeal />
                                </div>
                            </li>
                        </ul>
                    </div>
                </dnd-grid-box>
                <dnd-grid-box boxId="orderhistoryBox" dragSelector="span.move img">
                    <div class="page-left-2-3">
                        <Asset />
                    </div>
                </dnd-grid-box>
            </dnd-grid-container>
        </div>
    </div>
</template>

<script>
import { Container, Box } from "@dattn/dnd-grid";
import "@dattn/dnd-grid/dist/dnd-grid.css";
import Coinlist from "./coinlist/Index";
import Kline from "./kline/Index";
import Trade from "./trade/Index";
import Asset from "./asset/Index";
import Pendingorder from "./pendingorder/Index";
import Latestdeal from "./latestdeal/Index";
import Stats from "./Stats";
import Submenu from "./Submenu";
export default {
    name: "Content",
    components: {
        Coinlist,
        Kline,
        Trade,
        Asset,
        Pendingorder,
        Latestdeal,
        Stats,
        Submenu,
        DndGridContainer: Container,
        DndGridBox: Box
    },
    data() {
        return {
            layout: !1,
            //tab The two columns on the right show the subscript
            tab: 1,
            //status Right side double column merge switch
            status: 0,
           
            cellSize: {
                w: this.findWidth(),
                h: 100
            },
            maxColumnCount: 12,
            maxRowCount: Infinity,
            bubbleUp: false,
            margin: 10,
            boxCount: 5,
            layoutJson: [
                {
                    id: "coinlistBox",
                    hidden: false,
                    pinned: false,
                    position: {
                        x: 0,
                        y: 0,
                        w: 3,
                        h: 8
                    }
                },
                {
                    id: "klinebox",
                    hidden: false,
                    pinned: false,
                    position: {
                        x: 3,
                        y: 0,
                        w: 6,
                        h: 5
                    }
                },
                {
                    id: "lastpriceBox",
                    hidden: false,
                    pinned: false,
                    position: {
                        x: 9,
                        y: 0,
                        w: 3,
                        h: 8
                    }
                },

                {
                    id: "tradesbox",
                    hidden: false,
                    pinned: false,
                    position: {
                        x: 3,
                        y: 5,
                        w: 6,
                        h: 2
                    }
                },
                {
                    id: "orderhistoryBox",
                    hidden: false,
                    pinned: false,
                    position: {
                        x: 0,
                        y: 8,
                        w: 12,
                        h: 2
                    }
                },
                {
                    id: 4,
                    hidden: false,
                    pinned: false,
                    position: {
                        x: 6,
                        y: 2,
                        w: 3,
                        h: 1
                    }
                }
            ],
            tabs: []
        };
    },
	
	 watch: {
    windowHeight(newHeight, oldHeight) {
     this.txt = `it changed to ${newHeight} from ${oldHeight}`;
    }
  },

  mounted() {
    this.$nextTick(() => {
      window.addEventListener('resize', this.onResize);
    })
  },

  beforeDestroy() { 
    window.removeEventListener('resize', this.onResize); 
  },
    created() {
        let that = this;
        window.onresize = () => {
            that.resetLayout();
        };
        that.resetLayout();
    },
    methods: {
        resetLayout() {
            let width = window.innerWidth;
            if (width < 2400 && this.status == 0) {
                this.status = 1;
            } else if (width > 2400 && this.status != 0) {
                this.status = 0;
            }
			
            this.$store.commit("addResize", 1);
        },
		onResize() {
      this.windowHeight = window.innerHeight
        },
        findWidth(){
            
            let gowidth=0;
            if(window.innerWidth<780){
                gowidth=100;
            }else{
                gowidth=window.innerWidth/12 -11;
            }
            console.log(window.innerWidth+"<- Inner width and Gowidth="+gowidth);
            return gowidth;
        },
        checkTab(tab) {
            if (this.status == 0) {
                return !1;
            }
            this.tab = tab;
        }
    }
};
</script>



