<template>
    <div class="topspace">
        <ul id="topstats">
            <li class="marketname">
                <h2 id="ts-marketname">
                    {{ $store.state.marketInfo.title.replace("_", " / ").toLocaleUpperCase() }}
                    <img
                        v-if="$store.state.marketInfo.img"
                        :src="
                            $store.state.localhost +
                                '/Upload/coin/' +
                                $store.state.marketInfo.img
                        "
                    />
                </h2>
            </li>
            <li class="widthpercent">
                <h5>LAST PRICE</h5>
                <h2 id="ts-lastprice" class="statup">
                    <span
                        :class="
                            $store.state.socketTopic.rate * 1 - 0 > 0
                                ? 'icon-arrow-up'
                                : 'icon-arrow-down'
                        "
                        >{{ $store.state.socketTopic.price * 1 }}</span
                    >
                </h2>
            </li>
            <li class="changes24 widthpercent">
                <h5>24H CHANGES</h5>
                <h2 id="ts-change24" class="statdown">
                    <span
                        :class="
                            $store.state.socketTopic.rate * 1 - 0 > 0
                                ? 'icon-arrow-up'
                                : 'icon-arrow-down'
                        "
                        >{{
                            ($store.state.socketTopic.rate * 1 - 0 > 0
                                ? "+"
                                : "") + $store.state.socketTopic.rate
                        }}%</span
                    >
                </h2>
            </li>
            <li class="changes24 widthpercent">
                <h5 class="high-stat">24H</h5>
                <h2 id="ts-high24">{{ $store.state.socketTopic.high * 1 }}</h2>
            </li>
            <li class="changes24 widthpercent">
                <h5 class="low-stat">24H</h5>
                <h2 id="ts-low24h">{{ $store.state.socketTopic.low * 1 }}</h2>
            </li>
            <li class="changes24 widthpercent">
                <h5>24H VOLUME</h5>
                <h2 id="ts-vol24h">
                    {{ ($store.state.socketTopic.volume * 1).toFixed(0) }}
                    {{
                        $store.state.marketInfo.market
                            .toLocaleUpperCase()
                            .split("_")[0]
                    }}
                </h2>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    name: "Menu"
};
</script>



