Math.toFixed = function(number,length){
	if(isNaN(number) || isNaN(length) || !length || length < 0){
		return number;
	}
	var str0 = '';
	for(var i = 0; i < length; i++){
		str0 += '0';
	}
	var str = number + '';
	var arr = str.split('.');
	var _number = '';
	if(arr.length == 1){
		_number = arr[0]+'.'+str0;
	}else{
		_number = arr[0]+'.'+(arr[1]+str0).substr(0,length);
	}
	return _number;
}
Object.defineProperty(Math, 'toFixed', {'enumerable': false});
export default Math


