import Vue from "vue";
import Vuex from "vuex";

import Cookies from "../global/cookies";

Vue.use(Vuex);

let language = Cookies.get("think_language") || "en-us";
let skin = Cookies.get("skin") || "black";
if (skin == "white") {
    document.body.className = "skin-white";
}
let match = window.location.href.match(/\/market\/([^\/]*)/);
let market = "btc_usdt";
if (match && match[1]) {
    market = match[1];
}
const state = {
    skin: skin,
    dev: Cookies.get("trade_dev"),
    localhost: Cookies.get("trade_dev") ? "" : "",
    servicehost: Cookies.get("trade_dev") ? "/api/" : "/api/",
    language: language,
    login: !1,
    initMarket: !1,
    entrustDepth: 2,
    resize: 0,
    coinlist: {},
    usd: {
        usd: 0,
        coin: 0
    },
    navigation: {},
    userInfo: {
        usd: "0",
        usdd: "0",
        mobile: "",
        nickName: "",
        token: "",
        userid: "",
        vip: {
            usertype: "1",
            fee_discounts: "0%"
        }
    },
    marketInfo: {
        round: 6,
        step: "0.1",
        p_round: 2,
        p_step: "0.1",
        market: market,
        title: "",
        ws: "",
        img: ""
    },
    tradeLog: [],
    entrustDepthRound: 2,
    tradingview_config: {},
    tradingview: {},
    tradePriceBuy: "",
    tradePriceSell: "",
    tradeVolumeBuy: "",
    tradeVolumeSell: "",
    tradeNicePrice: "0",
    tradeNicePriceBuy: "0",
    tradeNicePriceSell: "0",
    update: {},
    fav: {},
    socketState: !1,
    socketTopic: {
        amount: 0,
        high: 0,
        id: 0,
        low: 0,
        price: 0,
        rate: 0,
        volume: 0,
        name: ""
    },
    socketLatestdeal: [],
    socketPendingOrderBuy: [],
    socketPendingOrderSell: [],
    socketCoinList: [],
    socketTrade: {},
    socketAsset: [],
    socketDepth: {
        buy: [],
        sell: [],
        price: 0
    }
};

const mutations = {
    changeSkin(state, skin) {
        state.skin = skin;
    },
    setUSD(state, usd) {
        state.usd = usd;
    },
    setLogin(state, login) {
        state.login = login;
    },
    setCoinlist(state, coinlist) {
        state.coinlist = coinlist;
    },
    setNavigation(state, navigation) {
        state.navigation = navigation;
    },
    setInitMarket(state, initMarket) {
        state.initMarket = initMarket;
    },
    setUserInfo(state, userInfo) {
        state.userInfo = userInfo;
    },
    setMarketInfo(state, marketInfo) {
        state.marketInfo = marketInfo;
    },
    changeLanguage(state, language) {
        state.language = language;
    },
    changeLang(state, lang) {
        state.lang = lang;
    },
    addTradingview(state, tradingview) {
        state.tradingview = tradingview;
    },
    addTradingviewConfig(state, tradingview_config) {
        state.tradingview_config = tradingview_config;
    },
    setTradePrice(state, price) {
        state.tradePriceBuy = price;
        state.tradePriceSell = price;
    },
    setTradePriceBuy(state, price) {
        state.tradePriceBuy = price;
    },
    setTradePriceSell(state, price) {
        state.tradePriceSell = price;
    },
    setTradeVolume(state, volume) {
        state.tradeVolumeBuy = volume;
        state.tradeVolumeSell = volume;
    },
    setTradeVolumeBuy(state, volume) {
        state.tradeVolumeBuy = volume;
    },
    setTradeVolumeSell(state, volume) {
        state.tradeVolumeSell = volume;
    },
    setTradeNicePrice(state, price) {
        state.tradeNicePrice = price;
        //state.tradePriceSell = price
    },
    setTradeNicePriceBuy(state, price) {
        state.tradeNicePriceBuy = price;
        //state.tradePriceSell = price
    },
    setTradeNicePriceSell(state, price) {
        //state.tradePriceBuy = price
        state.tradeNicePriceSell = price;
    },
    addResize(state, resize) {
        state.resize++;
    },
    setUpdate(state, update) {
        state.update = update;
    },
    setFav(state, fav) {
        state.fav = fav;
    },
    changeSocketTopic(state, socketTopic) {
        state.socketTopic = socketTopic;
    },
    changeSocketLatestdeal(state, socketLatestdeal) {
        state.socketLatestdeal = socketLatestdeal;
    },
    changeSocketPendingOrderBuy(state, socketPendingOrderBuy) {
        state.socketPendingOrderBuy = socketPendingOrderBuy;
    },
    changeSocketPendingOrderSell(state, socketPendingOrderSell) {
        state.socketPendingOrderSell = socketPendingOrderSell;
    },
    changeSocketCoinList(state, socketCoinList) {
        state.socketCoinList = socketCoinList;
    },
    changeSocketTrade(state, socketTrade) {
        state.socketTrade = socketTrade;
    },
    changeSocketAsset(state, socketAsset) {
        state.socketAsset = socketAsset;
    },
    changeSocketState(state, socketState) {
        state.socketState = socketState;
    },
    changeSocketEntrustDepth(state, entrustDepth) {
        state.entrustDepth = entrustDepth;
    },
    changeEntrustDepthRound(state, entrustDepthRound) {
        state.entrustDepthRound = entrustDepthRound;
    },
    changeSocketDepth(state, socketDepth) {
        state.socketDepth = socketDepth;
    },
    changeTradeLog(state, tradeLog) {
        state.tradeLog = tradeLog;
    }
};

export default new Vuex.Store({
    state,
    mutations
});



