<template>
	<div class="content-unit bjs-tab">
        <div class="content-pad">
            <div class="bjs-tab-head">
                <ul class="clearfix flex-box">
                    <li class="">
                        <a @click="change('btc','BTC')" href="javascript:;" :class="state.tab == 'btc' ? 'active':''"><span>BTC</span></a>
                    </li>
                    <li class="">
                        <a @click="change('eth','ETH')" href="javascript:;" :class="state.tab == 'eth' ? 'active':''"><span>ETH</span></a>
                    </li>
                    <li>
                        <a @click="change('usdt','USDT')" href="javascript:;" :class="state.tab == 'usdt' ? 'active':''"><span>USDT</span></a>
                    </li>
					<li>
                        <a @click="change('usd','USD')" href="javascript:;" :class="state.tab == 'usd' ? 'active':''"><span>USD</span></a>
                    </li>
					<li class="hide">
                        <a @click="change('limited',$lang.coinlistLimited)" href="javascript:;" :class="state.tab == 'limited' ? 'active':''"><span>{{ $lang.coinlistLimited }}</span></a>
                    </li>
                    <!-- <li>
                        <a @click="change('fav',$lang.coinlistFav)" href="javascript:;" :class="state.tab == 'fav' ? 'active':''"><span>{{ $lang.coinlistFav }}</span></a>
                    </li> -->
                </ul>
				<span class="move"><img src="@/assets/images/move.svg"></span>
            </div>
            <div class="bjs-search">
                <div class="clearfix">
                    <div class="clearfix">
                        <div class="bjs-search-box">
                            <input type="text" name="keywords" :placeholder="$lang.coinlistsearch+searchType.toLocaleUpperCase()" class="form-input" v-model="keywords" @input="search">
                        </div>
                    </div>
                </div>
            </div>
            <div class="bjs-tab-cont font12">
                <div class="bjs-tab-cont-head tophgrey">
                    <ul class="clearfix bjs-tab-tr flex-box tophgrey">
                        <li class="flex-item-3"><span @click="sort('name')" class="curr-base">{{ $lang.coinlistCoins }}<em :class="(sortstate.name == 1 ? 'active ':'')+'bjs-sort-up'"></em><em :class="(sortstate.name == 2 ? 'active ':'')+'bjs-sort-down'"></em></span></li>
                        <li class="flex-item-3 tr"><span @click="sort('new_price')" class="curr-base">{{ $lang.coinlistPrice }}<em :class="(sortstate.new_price == 1 ? 'active ':'')+'bjs-sort-up'"></em><em :class="(sortstate.new_price == 2 ? 'active ':'')+'bjs-sort-down'"></em></span></li>
                        <li class="flex-item-3 tr"><span @click="sort('change')" class="curr-base">{{ $lang.coinlistChange }}<em :class="(sortstate.change == 1 ? 'active ':'')+'bjs-sort-up'"></em><em :class="(sortstate.change == 2 ? 'active ':'')+'bjs-sort-down'"></em></span></li>
                        <li class="tr"><span class="bjs-star star-cur"></span></li>
                    </ul>
                </div>
                <div class="bjs-tab-cont-wrap">
                    <div class="new-coin-list">
                        <List :usd="usd" :fav="fav" :coinlist="coinlistdata" :state="state" />
                    </div>
                </div>
            </div>
		</div>
	</div>
</template>

<script>
import List from './List'
export default {
    name: 'Coinlist',
    props: ['fav','coinlistdata'],
    data () {  	
        return {
            usd: this.$store.state.marketInfo.market.split('_')[1],
            searchType: this.$store.state.marketInfo.market.split('_')[1],
            keywords: '',
            sortstate: {
                name: 0,
                new_price: 0,
                change: 0
            },
            state: {
                tab: this.$store.state.marketInfo.market.split('_')[1],
                search: !1,
                fav_active: !1,
            }
        }
    },
    components: {
        List
    },
    methods: {
        search(){
            let that = this
            let val = that.keywords.trim()
                that.coinlistdata.forEach(elem => {
                    if(val == ''){
                        that.state.search = !1
                        elem.hide = !1
                    }else{
                        that.state.search = !0

                        if(elem.title.replace(/(\(|\/.*)/g,'').toLowerCase().search(val.toLowerCase()) == -1){
                            elem.hide = !0
                        }else{
                            elem.hide = !1
                        }
                    }
                    if(that.state.fav_active && !that.fav[elem.name]){
                        //If it is a collection state, the second judgment
                        elem.hide = !0
                    }
                })
        },
        sort(key){
            let that = this
            let sort = that.sortstate
            for(let i in sort){
                if(i != key){
                    sort[i] = 0
                }
            }
            if(sort[key] != 1){
                sort[key] = 1
            }else{
                sort[key] = 2
            }
            let data = that.coinlistdata
            data.insertSort((a,b) => {
                if(isNaN(a[key])){
                    return sort[key] == 1 ? (a[key] > b[key]) : (a[key] < b[key])
                }else{
                    return sort[key] == 1 ? (a[key]*1 > b[key]*1) : (a[key]*1 < b[key]*1)
                }               
            })
            let arr = []
            data.forEach(elem => {
                arr.push(elem)
            })
            that.$store.commit('changeSocketCoinList',arr)
        },
    	change(type,searchType){
            let that = this
            
            if(that.state.tab === type){
                return !1
            }
            if(type == 'fav'){
                if(!that.$store.state.login){
                    that.$layer.msg(that.$lang.tradeLogin,{icon: 1, time: 1000})
                    return !1
                }
                that.usd = ''
                that.state.tab = type
                that.state.fav_active = !0
                that.coinlistdata.forEach(elem => {
                  
                    if((that.state.search && !elem.hide) || !that.state.search){
                        //If it is the search state, only the visible elements are traversed, and the non-search state is traversed.
                        if(!that.fav[elem.name]){
                            elem.hide = !0
                        }else{
                            elem.hide = !1
                        }
                    }
                })
            }else{
                that.state.tab = type
                that.usd = type
                that.state.fav_active = !1
                that.coinlistdata.forEach(elem => {
 elem.hide = !1
                })
                if(that.state.search){
                    
                    //If it is a search status, all are set to visible and then search again
                    that.search()
                }
            }
            that.searchType = searchType
        }
    }
}
</script>



