<template>
	<div class="trading-buy pull-left">
	<div class="bjs-tab-head nobg">
		<ul class="flex-box">
          <li><a @click="tradeType('limit')">Limit</a></li>
          <li><a @click="tradeType('market')">Market</a></li>
          <li><a @click="tradeType('stop')">Stop-Limit</a></li>
		</ul>
	    </div>
	    <p class="trading-box-list text-overflow">
	        <span>{{ $lang.tradeAvbl }}</span><span class="c03ad6f pull-right font-12"> <span id="my_rmb">{{ asset }}</span> {{ usd.toLocaleUpperCase() }} </span>
	    </p>
        <div>
            <input class="hide-input" type="text" name="num">
	        <input class="hide-input" type="password" name="pwtrade">
        </div>
        <p class="input-group trading-box-list trading-input-group" v-show="isStop" >
	        <span class="input-group-addon">Stop Price</span><input ref="buy_stop" v-model="$store.state.tradeBuyStop" class="form-control" type="text" id="buy_stop" name="stop" min="0" :step="p_step">
	    </p>
	    <p class="input-group trading-box-list trading-input-group" v-show="!isMarket">
	        <span class="input-group-addon">{{ $lang.tradeBuyPrice }}</span><input ref="buy_price" v-model="$store.state.tradePriceBuy" id="buy_price" class="form-control" type="number" name="price" :placeholder="$lang.tradeBuyPriceplaceholder" min="0" :step="p_step">
	    </p>
	    <p class="input-group trading-box-list trading-input-group" >
	        <span class="input-group-addon">{{ $lang.tradeBuyVolume }}</span><input ref="buy_num" v-model="$store.state.tradeVolumeBuy" class="form-control" type="text" id="buy_num" name="num" min="0" :step="step" @input="changeprogress">
	    </p>

	    <div class="trading-box-list text-overflow">
            <ul class="flex-box tc">
                <li><span @click="progress(25)" :class="(progressnum == 25 ? 'btn-up ':'')+'active-btn bjs-btn-full'">25%</span></li>
                <li class="ml10"><span @click="progress(50)" :class="(progressnum == 50 ? 'btn-up ':'')+'active-btn bjs-btn-full'">50%</span></li>
                <li class="ml10"><span @click="progress(75)" :class="(progressnum == 75 ? 'btn-up ':'')+'active-btn bjs-btn-full'">75%</span></li>
                <li class="ml10"><span @click="progress(100)" :class="(progressnum == 100 ? 'btn-up ':'')+'active-btn bjs-btn-full'">100%</span></li>
            </ul>
	    </div>

	    
	    <p class="input-group trading-box-list trading-input-group">
	        <span class="input-group-addon">{{ !$store.state.login ? $lang.tradeTradePassword : setpaypassword == 1 ? $lang.tradeAlert2 : setpaypassword == 2 ? $lang.tradeAlert3 : $lang.tradeAlert4 }}</span>
	        <input class="form-control pr3-important" ref="buy_paypassword" id="buy_paypassword" name="pwtrade" type="password" v-model.trim="paypassword">
	        <span class="icon-setting setting-pay-password" :title="$lang.tradeSetting" @click="$emit('set_paypassword')">{{ $lang.tradeSetting }}</span>
	    </p>
	    <p class="trading-box-list cplaceholder text-overflow font-12" >
	        {{ $lang.tradeAmount }}:<span class="pull-right"><span id="buy_mum" class="c03ad6f font-12">{{ computedAmount }}</span> {{ usd.toLocaleUpperCase() }}</span>
	    </p>
	    <p class="trading-box-list">
            <Button :text="$store.state.login ? $lang.tradeBuy : $lang.menuLogin" @tap="trade" type="full.max.up" />
	    </p>
        
	</div>
</template>

<script>
import Button from '../../public/Button'
export default {
    name: 'Buy',
    props: ['step','p_step','setpaypassword','fee','niceprice','asset'],
    components: {
        Button
    },
    data () {
        return {
            router:'limit',
            isMarket:false,
            isStop:false,
        	progressnum: 0,
        	paypassword: '',
            price: 0,
            stop: 0,
        	quantity: '',
        	amount: '0',
            maxnum: '-',
            coin: this.$store.state.marketInfo.market.split('_')[0],
            nicepricestatus: !1,
            usd: this.$store.state.marketInfo.market.split('_')[1]
        }
    },
    computed: {
        computedAmount(){
		
            this.price = this.$store.state.tradePriceBuy
            this.quantity = this.$store.state.tradeVolumeBuy
            this.amount = (this.$store.state.tradePriceBuy * this.quantity * (1+this.fee*0.01)).toFixed(this.$store.state.marketInfo.p_round)
            this.stop= this.$store.state.tradeBuyStop
            if(!!this.price && this.price > 0){
                this.maxnum = Math.toFixed(this.asset / (1+this.fee*0.01) / this.price, this.$store.state.marketInfo.p_round)
            }else{
                this.maxnum = '-'
            }
            return this.amount
        }
    },
    watch: {
        niceprice(){
            if(this.niceprice != 0 && !this.nicepricestatus){
                this.nicepricestatus = !0
                this.price = this.$store.state.tradePriceBuy = this.niceprice
            }
        },
        price(){
            this.progressnum = 0
            let price = this.price+''
            let p_round = this.$store.state.marketInfo.p_round
            if(price.split('.')[1] && price.split('.')[1].length > p_round){
                this.price = this.$store.state.tradePriceBuy = price.split('.')[0]+'.'+price.split('.')[1].substr(0,p_round)
            }
        }
    },
    methods: {
    	trade(button){
            let that = this
            if(!that.$store.state.login){
                window.location.href = that.$store.state.localhost+'/login'
                return !1
            }
            let data = {
                price: that.price,
                num: that.quantity,
                paypassword: that.$md5(that.paypassword),
                market: that.$store.state.marketInfo.market,
                type: 1,
                stop:that.stop,
                tradeType:that.router
            }
            if(that.price == '' || that.price <= 0){
                that.$layer.tips(that.$lang.tradeTips1,'#buy_price',{tips:2})
                that.$refs.buy_price.focus()
                return !1
            }
            if(that.router=='stop' && (that.stop == '' || that.stop <= 0)){
                console.log(that.router+'running');
                console.log(that.stop+'running');
                that.$layer.tips(that.$lang.tradeTips1,'#buy_stop',{tips:2})
                that.$refs.buy_stop.focus()
                return !1
            }
            if(that.quantity == '' || that.quantity <= 0){
                that.$layer.tips(that.$lang.tradeTips2,'#buy_num',{tips:2})
                that.$refs.buy_num.focus()
                return !1
            }
            if(that.setpaypassword == 2 && that.paypassword == ''){
                that.$layer.tips(that.$lang.tradeTips3,'#buy_paypassword',{tips:2})
                that.$refs.buy_paypassword.focus()
                return !1
            }
            if(that.amount <= 0){
                return !1
            }
            //console.log(data)
            button.loading = !0
            let url = '/Trade/upTrade'
            that.axios.post(url,data).then((response) => {
                let data = response.data
                button.loading = !1
                if(data.status){
                    that.$layer.msg(data.info, {icon: 2, time: 3e3})
                    that.created()
                    that.quantity = ''
                    that.paypassword = ''
                    that.progressnum = 0
                    that.$store.commit('setTradeVolumeBuy',that.quantity)
                    that.$store.state.update.init()
                    
                }else{
                     that.$store.state.update.init()
                     that.created()
                    that.$layer.msg(data.info, {icon: 3, time: 2e3})
                }
            }).catch(() => {
                button.loading = !1
            });
        },
        changeprogress(){
            let that = this
            that.progressnum = 0
        },
        tradeType(type){
            let that = this
            console.log(type);
          switch(type){
              case 'market':
                    that.isStop=false;
                    that.isMarket=true;
                    that.router='market';
                    break;
              case 'limit':
                   that.isStop=false;
                    that.isMarket=false;
                    that.router='limit';
                    break;
              case 'stop':
                    that.isStop=true;
                    that.isMarket=false;
                    that.router='stop';
                    break;
               default:
                    that.isStop=false;
                    that.isMarket=false;
                    that.router='limit';
          }
        console.log(that.isMarket);
        },
    	progress(number){
    		let that = this
            if(that.progressnum == number){
                that.progressnum = 0
                return !1
            }
    		if(that.price == '' || isNaN(that.price)){
                that.$layer.tips(that.$lang.tradeTips1,'#buy_price',{tips: 2, time: 1000})
    			return !1
    		}
    		that.progressnum = number
            if(that.asset == 0){
                that.quantity = Math.toFixed('0.0000',this.$store.state.marketInfo.p_round)
                that.$store.commit('setTradeVolumeBuy',that.quantity)
                return !1
            }
            that.quantity = Math.toFixed(that.asset/ (1+that.fee*0.01) * number / 100 / that.price, this.$store.state.marketInfo.p_round)
            that.$store.commit('setTradeVolumeBuy',that.quantity)
    	},
    	set_price(){
    		let that = this
    		that.$store.commit('setTradePriceBuy',that.niceprice)
    	}
    }
}
</script>



