/**
 * JS API
 */
import dataUpdater from './dataUpdater'
class datafeeds {

  /**
   * JS API
   * @param {*Object} vue Vue instance
   */
  constructor(vue) {
    this.self = vue
    this.barsUpdater = new dataUpdater(this)
  }

  /**
   * @param {*Function} callback  Callback
   * `onReady` should return result asynchronously.
   */
  onReady(callback) {
    return new Promise((resolve, reject) => {
      let configuration = this.defaultConfiguration()
      if (this.self.getConfig) {
        configuration = Object.assign(this.defaultConfiguration(), this.self.getConfig())
      }
      resolve(configuration)
    }).then(data => callback(data))
  }

  /**
   * @param {*String} symbolName  Symbol name or ticker
   * @param {*Function} onSymbolResolvedCallback Successful callback
   * @param {*Function} onResolveErrorCallback   Failure callback
   * `resolveSymbol` should return result asynchronously.
   */
  resolveSymbol(symbolName, onSymbolResolvedCallback, onResolveErrorCallback) {
    return new Promise((resolve, reject) => {
      let symbolInfo = this.defaultSymbol()
      if (this.self.getSymbol) {
        symbolInfo = Object.assign(this.defaultSymbol(), this.self.getSymbol())
      }
      resolve(symbolInfo)
    }).then(data => onSymbolResolvedCallback(data)).catch(err => onResolveErrorCallback(err))
  }

  /**
	* @param {*Object} symbolInfo Product Information Object
 * @param {*String} resolution resolution
 * @param {*Number} rangeStartDate timestamp, k-line time of the leftmost request
 * @param {*Number} rangeEndDate timestamp, K-line time of the rightmost request
 * @param {*Function} onDataCallback callback function
 * @param {*Function} onErrorCallback callback function
   */
  getBars(symbolInfo, resolution, rangeStartDate, rangeEndDate, onDataCallback, onErrorCallback) {
    const onLoadedCallback = data => {
      data && data.length ? onDataCallback(data, { noData: true }) : onDataCallback([], { noData: true })
    }
    this.self.getBars(symbolInfo, resolution, rangeStartDate, rangeEndDate, onLoadedCallback)
  }

  /**
	* Subscribe to K-line data. The chart library will call the onRealtimeCallback method to update the live data.
 * @param {*Object} symbolInfo Product Information
 * @param {*String} resolution resolution
 * @param {*Function} onRealtimeCallback callback function
 * @param {*String} unique identifier for subscriberUID listening
 * @param {*Function} onResetCacheNeededCallback (starting with 1.7): will be executed when the bar data changes
   */
  subscribeBars(symbolInfo, resolution, onRealtimeCallback, subscriberUID, onResetCacheNeededCallback) {
    this.barsUpdater.subscribeBars(symbolInfo, resolution, onRealtimeCallback, subscriberUID, onResetCacheNeededCallback)
  }

  /**
	* Unsubscribe from K-line data
 * @param {*String} unique identifier for subscriberUID listening
   */
  unsubscribeBars(subscriberUID) {
    this.barsUpdater.unsubscribeBars(subscriberUID)
  }

  /**
   *default allocation
   */
  defaultConfiguration() {
    return {
      supports_search: true,
      supports_group_request: false,
      supported_resolutions: ['1', '5', '15', '30', '60', '1D', '2D', '3D', '1W', '1M'],
      supports_marks: true,
      supports_timescale_marks: true,
      supports_time: true
    }
  }

  /**
   * Default product information
   */
  defaultSymbol() {
    let p_round = this.self.$store.state.marketInfo.p_round
    let pricescale = Math.pow(10,p_round)
    return {
      'name': this.self.$store.state.marketInfo.market.replace(/_/,'/').toLocaleUpperCase(),
      'timezone': 'Asia/Shanghai',
      'minmov': 1,
      'minmov2': 0,
      'pointvalue': 1,
      'fractional': false,
      'session': '24x7',
      'has_intraday': true,
      'has_no_volume': false,
      'description': this.self.$store.state.marketInfo.market.replace(/_/,'/').toLocaleUpperCase(),
      'pricescale': pricescale,
      'ticker': this.self.$store.state.marketInfo.market.replace(/_/,'/').toLocaleUpperCase(),
      'supported_resolutions': ['1', '5', '15', '30', '60', '1D', '2D', '3D', '1W', '1M']
    }
  }
}

export default datafeeds


